<template>
  <div :class="['e-tooltip', position, `e-tooltip__${theme}`]">

    <span class="e-tooltip__label">
      <slot name="label" />
    </span>
    <div class="e-tooltip__icon">?</div>
    <div class="e-tooltip__content">
      <slot name="message" />
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'e-tooltip',

  props: ['position'],

  computed: {
    ...mapGetters({
      theme: 'ui/getTheme',
    }),
  },

};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-tooltip {
  $this: &;
  position: relative;
  display: inline-flex;
  align-items: center;

  &:hover {
    z-index: 1;
    #{$this}__content {
      opacity: 1;
      top: -2px;
    }

    &.left {
      #{$this}__content {
        transform: translate(calc(-100% - 9px), 0);
      }
    }

    &.right {
      #{$this}__content {
        transform: translate(calc(100% + 9px), 0);
      }
    }

    #{$this}__icon {
      background: #2D2F72;
      border-color: #2D2F72;
      color: #fff;
    }
  }

  &.left {
    #{$this}__content {
      left: 0;
      justify-content: flex-end;
      text-align: right;
      transform: translate(calc(-100% - 5px), 0);

      &:before {
        border-left: 6px solid #2D2F72;
        right: -5px;
      }
    }
  }

  &.right {
    #{$this}__content {
      right: 0;
      justify-content: flex-start;
      text-align: left;
      transform: translate(calc(100% + 5px), 0);

      &:before {
        border-right: 6px solid #2D2F72;
        left: -5px;
      }
    }
  }

  &__label {
    border-bottom-width: 1px;
    border-bottom-style: dotted;
    margin-bottom: -2px;
  }

  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 10px;
    height: 10px;
    color: #9ea1bd;
    font-size: 8px;
    border: 1px solid #9ea1bd;
    border-radius: 50%;
    background-color: transparent;
    margin-left: 3px;
    transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out;
  }

  &__content {
    display: flex;
    align-items: center;
    padding: 8px;
    box-sizing: border-box;
    opacity: 0;
    top: -9999px;
    position: absolute;
    font-size: 10px;
    line-height: 14px;
    font-weight: 500;
    color: #fff;
    background: #2D2F72;
    border-radius: 5px;
    width: 205px;
    margin-top: -6px;
    text-transform: none;
    transition: opacity .2s ease-in-out, transform .2s ease-in-out;

    &:before {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      top: 8px;
      border-top: 6px solid transparent;
      border-bottom: 6px solid transparent;
    }
  }

  &__dark {
    #{$this}__icon {
      color: #8D9098;
      border-color: #8D9098;
    }

    #{$this}__content {
      background: #0497FE;
      color: #fff;
    }

    &.left {
      #{$this}__content::before {
        border-left-color: #0497FE;
      }
    }

    &.right {
      #{$this}__content::before {
        border-right-color: #0497FE;
      }
    }

    &:hover {
      #{$this}__icon {
        background: #0497FE;
        border-color: #0497FE;
        color: #fff;
      }
    }
  }
}
</style>



