<template>
  <div class="the-footer">
    <div class="the-footer__shadow" />
    <div class="the-footer__container">
      <div class="the-footer__socials">
        <the-socials/>
      </div>
      <div class="the-footer__body">
        <div class="the-footer__column the-footer__column_first">
          <a href="https://dmex.app/" target="_blank" class="the-footer__logo">
            <e-icon :src="iconDmexLogo" />
          </a>
          <div class="the-footer__btn-mob">
            <e-button
              fill="green"
              size="s-relative"
              width="full"
              adaptiveMod="gutter-top"
              @click="onWalletButtonClick"
            >
              Import wallet
            </e-button>
          </div>
          <div class="the-footer__copy">
            Copyright © 2017-{{currentYear}} — Dmex.App
          </div>
        </div>
        <div class="the-footer__column the-footer__column_second">
          <div class="the-footer__label">Info</div>
          <ul class="the-footer__nav">
            <li class="the-footer__nav-item">
              <router-link
                :to="{ name: 'news' }"
                class="the-footer__nav-link"
              >News
              </router-link>
            </li>
            <li class="the-footer__nav-item">
              <router-link
                :to="{ name: 'fees' }"
                class="the-footer__nav-link"
              >Fees
              </router-link>
            </li>
            <li class="the-footer__nav-item">
              <router-link
                :to="{ name: 'faq' }"
                class="the-footer__nav-link"
              >
                FAQ
              </router-link>
            </li>
            <li class="the-footer__nav-item">
              <router-link
                :to="{ name: 'support' }"
                class="the-footer__nav-link"
              >
                Contact US
              </router-link>
            </li>
          </ul>
        </div>

        <div class="the-footer__column the-footer__column_third">
          <div class="the-footer__label">For traders</div>
          <ul class="the-footer__nav">
            <li class="the-footer__nav-item">
              <router-link
                :to="$popupRoute(POPUP_SMART_CONTRACTS)"
                class="the-footer__nav-link"
              >
                Smart contracts
              </router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import getIntercom from '@/util/getIntercom';
import { routesMixin } from '@/util/mixins';
import { POPUP_IMPORT, POPUP_MY_WALLET } from '@/store/constants';

// Components
import TheLogo from '@/containers/blocks/TheLogo';
import TheSocials from '@/containers/blocks/TheSocials';
import EIcon from '@/components/EIcon';
import EButton from '@/components/EButton';

// Icons
import iconDmexLogo from '@/icons/e-icon__dmex-logo.svg';
import iconChat from '@/icons/e-icon__chat.svg';


export default {
  name: 'the-footer',

  data() {
    return {
      iconDmexLogo,
      iconChat,
      currentYear: new Date().getFullYear(),
    };
  },

  computed: {
    ...mapGetters({
      isLogin: 'wallets/isLogin',
    }),
  },

  created() {
    this.$store.dispatch('wallets/loadPrivateWallets');
  },

  methods: {
    onWalletButtonClick() {
      const popupPage = this.isLogin ? POPUP_MY_WALLET : POPUP_IMPORT;
      this.$router.push(this.$popupRoute(popupPage));
    },

    /**
     * Открытие окна чата
     */
    handleShowChat() {
      getIntercom()('show');
    },
  },

  components: {
    TheLogo,
    EIcon,
    TheSocials,
    EButton,
  },

  mixins: [routesMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-footer {
  margin-top: 0;
  width: 100%;
  background-color: #F2F2F2;
  padding-bottom: 40px;
  position: relative;
  z-index: 2;

  @include r(768) {
    background: #f2f2f2;
  }

  &__shadow {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 140px;
    overflow: hidden;

    &:before {
      content: '';
      position: absolute;
      left: 50%;
      transform: translate(-50%, 0);
      z-index: 0;
      top: -140px;
      width: 100%;
      max-width: 1600px;
      height: 140px;
      background: #848484;
      border-radius: 50%;
      filter: blur(24px);
      opacity: .32;
    }
  }

  &__container {
    max-width: 979px;
    padding: 0 20px;
    margin: 0 auto;
    box-sizing: border-box;
    position: relative;
    z-index: 1;

    @include rmin(2100) {
      max-width: 1220px;
    }

    @include rmin(2400) {
      max-width: 1480px;
    }
  }

  &__socials {
    position: relative;
    top: -25px;

    @include r(768) {
      padding-bottom: 16px;
    }

    @include r(374) {
      top: -21px;
      padding-bottom: 9px;
    }
  }

  &__body {
    display: flex;
    padding-top: 47px;

    @include r(768) {
      flex-direction: column;
      padding-top: 17px;
    }

    @include r(374) {
      padding-top: 13px;
    }

    @include rmin(2400) {
      padding-top: 32px;
    }
  }

  &__column {
    flex-basis: 20%;
    min-width: 20%;
    box-sizing: border-box;

    @include r(768) {
      flex-basis: 100%;
      min-width: 100%;
      display: flex;
      justify-content: center;
      flex-direction: column;
      padding: 0;
    }
  }

  &__column_first {
    padding-right: 50px;
    flex-basis: 40%;

    @include r(768) {
      order: 3;
      flex-basis: 100%;
      margin: 0 -20px -40px;
      padding: 45px 0 40px;
      background: #fff;
    }

    @include r(1050) {
     flex-basis: 29%
    }

    @include rmin(2400) {
      flex-basis: 59%;
      min-width: 59%;
    }
  }

  &__column_second {
    padding-left: 36px;
    margin-left: auto;

    @include r(768) {
      margin-bottom: 29px;
      padding-left: 0;
    }

    @include r(1050) {
      padding-left: 20px;
    }

    @include r(640) {
      padding-left: 0;
    }

    @include r(374) {
      margin-bottom: 19px;
    }

    @include rmin(2400) {
      flex-basis: 23%;
      min-width: 23%;
      padding-left: 11px;
    }
  }

  &__column_third {
    padding-left: 44px;

    @include r(768) {
      margin-bottom: 55px;
      padding-left: 0;
    }

    @include r(1050) {
      padding-left: 20px;
    }

    @include r(640) {
      padding-left: 0;
    }

    @include r(374) {
      margin-bottom: 35px;
    }

    @include rmin(2400) {
      flex-basis: 18%;
      min-width: 18%;
    }
  }

  &__column_fourth {
    padding-left: 44px;

    @include r(768) {
      margin-bottom: 55px;
      padding-left: 0;
    }

    @include r(1050) {
      padding-left: 20px;
    }

    @include r(640) {
      padding-left: 0;
    }

    @include r(374) {
      margin-bottom: 35px;
    }

    @include rmin(2400) {
      flex-basis: 18%;
      min-width: 18%;
    }

    @include r(768) {
      text-align: center;

      .e-button {
        width: 222px;
        font-size: 17px !important;
        line-height: 17px !important;
        margin: 0 auto;
      }
    }
  }

  &__logo {
    width: 99px;
    height: 44px;
    margin-top: 8px;
    margin-bottom: 20px;
    display: block;
    text-decoration: none;

    @include r(992) {
      margin: 0 auto 20px;
      width: 180px;
    }

    @include r(374) {
      margin: 0 auto 21px;
    }
  }

  &__copy {
    font-size: 13px;
    line-height: 22px;
    letter-spacing: -.5px;
    color: #92A2C3;
    font-weight: normal;
    max-width: 250px;

    br {
      display: none;
    }

    @include r(768) {
      text-align: center;
      margin: 0 auto;

      br {
        display: block;
      }
    }

    @include rmin(2400) {
      max-width: 290px;
    }
  }

  &__nav {
    padding-left: 0;
    margin: 0;

    @include r(768) {
      text-align: center;
    }
  }

  &__nav-item {
    list-style: none;

    &:not(:last-child) {
      margin-bottom: 19px;

      @include r(768) {
        margin-bottom: 29px;
      }

      @include r(374) {
        margin-bottom: 19px;
      }
    }
  }

  &__nav-link {
    color: #1F2EE9;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: .5px;
    text-decoration: none;
    position: relative;
    line-height: 1;
    cursor: pointer;
    padding-bottom: 4px;
    overflow: hidden;
    display: inline-block;

    &::after {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 2px;
      background: #1F2EE9;
      opacity: .2;
      transform: translate(-100%, 0);
      transition: all .2s ease-in-out;
    }

    &:hover {
      color: #333;

      &::after {
        transform: translate(0, 0);
        opacity: 1;
      }
    }

    @include r(768) {
      font-size: 14px;
      letter-spacing: .83px
    }

    @include r(374) {
      font-size: 13px;
      letter-spacing: .81px;
    }
  }

  &__button {
    width: 180px;
    padding-top: 22px;

    @include r(768) {
      margin: 0 auto;
      display: none;
    }
  }

  &__btn-link {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: #fff;
  }

  &__btn-mob {
    display: none;
    width: 200px;
    margin: 0 auto 30px;
  }

  &__label {
    color: #92A2C3;
    font-size: 11px;
    text-transform: uppercase;
    font-weight: normal;
    line-height: 21px;
    letter-spacing: .5px;
    padding-bottom: 18px;

    @include r(768) {
      text-align: center;
    }
  }

  &__button-chat {
    color: #1F2EE9;

    .e-icon {
      width: 20px !important;
      height: 20px !important;

      svg path {
        fill: #1F2EE9
      }
    }

    &:hover {
      color: #1F2EE9;

      .e-icon {
        svg path {
          fill: #1F2EE9;
        }
      }
    }
  }
}
</style>



