<template>
<div class="the-menu">
  <div class="the-menu__in">
    <div class="the-menu__close">
      <router-link
        :to="$closedPopupRoute"
        class="the-menu__clone"
      >
        <e-icon :src="iconClose"></e-icon>
      </router-link>
    </div>

    <div class="the-menu__logo">
      <the-logo isVersion/>
    </div>

    <div class="the-menu__list" data-test="menuNav">
      <div class="the-menu__group">
        <div class="the-menu__heading">Your wallet</div>
        <div class="the-menu__links">
          <router-link
            v-if="!isLogin"
            :to="$popupRoute(POPUP_IMPORT)"
            active-class="is-active"
            class="the-menu__link"
          >
            Connect
          </router-link>

          <router-link
            v-if="isLogin"
            :to="$popupRoute(POPUP_HISTORY)"
            active-class="is-active"
            class="the-menu__link"
          >
            Transaction history
          </router-link>
        </div>
      </div>

      <div class="the-menu__group">
        <div class="the-menu__heading">Need support?</div>
        <div class="the-menu__links">
          <a
            href="https://github.com/dmex-app/help-docs"
            active-class="is-active"
            class="the-menu__link"
            target="_blank"
          >
            Help center
          </a>

          <router-link
            :to="{ name: 'aboutus' }"
            class="the-menu__link"
          >
            About
          </router-link>

          <router-link
            :to="{ name: 'faq' }"
            class="the-menu__link"
          >
            FAQ
          </router-link>

          <router-link
            :to="{ name: 'fees' }"
            class="the-menu__link"
          >
            Fees
          </router-link>

          <router-link
            :to="{ name: 'support' }"
            class="the-menu__link"
          >
            Contact us
          </router-link>
        </div>
      </div>

      <div class="the-menu__group">
        <div class="the-menu__heading">Other</div>
        <div class="the-menu__links">
          <router-link
            :to="{ name: 'news' }"
            active-class="is-active"
            class="the-menu__link"
          >
            News
          </router-link>
          <router-link
            :to="$popupRoute(POPUP_SMART_CONTRACTS)"
            active-class="is-active"
            class="the-menu__link"
          >
            Smart contracts
          </router-link>
        </div>
      </div>
    </div>
    <div class="the-menu__lines"><e-icon :src="iconLines" /></div>
  </div>
</div>
</template>

<script>
import { mapGetters } from 'vuex';

import EIcon from '@/components/EIcon';
import TheWalletPagesPopup from '@/containers/blocks/TheWalletPagesPopup';
import TheLogo from '@/containers/blocks/TheLogo';
import EButton from '@/components/EButton';

import iconClose from '@/icons/e-icon_close.svg';
import iconWallet from '@/icons/e-icon_wallet.svg';
import iconAffiliate from '@/icons/e-icon_bag.svg';
import iconTokenCatalogue from '@/icons/e-icon_token-catalogue.svg';
import iconHelp from '@/icons/e-icon_search-with-dotts.svg';
import iconListToken from '@/icons/e-icon_plus-double.svg';
import iconSupport from '@/icons/e-icon_chatbubble-question.svg';
import iconDoc from '@/icons/e-icon_doc.svg';
import iconLines from '@/icons/e-icon_lines.svg';

import { routesMixin } from '@/util/mixins';

export default {
  name: 'the-menu',

  computed: {
    ...mapGetters({
      isLogin: 'wallets/isLogin',
    }),
  },

  created() {
    Object.assign(this, {
      iconClose,
      iconWallet,
      iconAffiliate,
      iconTokenCatalogue,
      iconHelp,
      iconListToken,
      iconSupport,
      iconDoc,
      iconLines,
    });
  },

  components: {
    EIcon,
    TheWalletPagesPopup,
    TheLogo,
    EButton,
  },

  mixins: [routesMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

$delay: 0.5s;
$delay-stagger: 0.08s;
$nav-items: 8;

.the-menu {
  $this: &;

  height: 100%;
  overflow-x: hidden;
  width: 100%;

  &__in {
    justify-content: center;
    display: flex;
    height: 100%;
    flex-direction: column;
    position: relative;
    z-index: 2;
    width: 100%;
    box-sizing: border-box;
    // padding: 11.9% 17.5%;
    padding: 32px 0 20px 35px;

    @include r(1200) {
      padding: 32px 0 32px 20px;
    }
  }

  &__logo {
    margin-bottom: 5px;
    position: absolute;
    top: 19px;
  }

  &__close {
    position: absolute;
    top: 20px;
    right: 21px;
    width: 12px;
    height: 12px;
    cursor: pointer;

    .e-icon svg {
      fill: #fff;
      opacity: .44;
      transition: opacity .15s ease-in-out;
    }

    &:hover {
      .e-icon svg {
        opacity: 1;
      }
    }
  }

  &__group {
    padding-top: 24px;
    padding-bottom: 20px;
    border-top: 1px solid rgba(17,131,147,.34);
  }

  &__heading {
    font-size: 9px;
    color: $color-green;
    text-transform: uppercase;
    letter-spacing: .69px;
    font-weight: 700;
    margin-bottom: 16px;
  }

  &__links {
    display: flex;
    flex-direction: column;
  }

  &__link {
    cursor: pointer;
    color: rgba(175, 184, 190, .5);
    font-size: 16px;
    text-decoration: none;
    letter-spacing: .2px;
    transition: $transition-time;
    font-weight: 500;

    &:not(:last-child) {
      margin-bottom: 8px;
    }

    &:hover {
      color: #fff;
    }

    &.is-active {
      color: #fff;
    }
  }

  &__link_primary {
    font-size: 17px;
  }

  &__lines {
    position: absolute;
    z-index: -1;
    bottom: 0;
    left: 0;
    width: 145px;
    height: 90px;
  }

  @include h(750) {
    &__heading {
      margin-bottom: 16px;
    }

    &__group {
      padding-top: 20px;
      padding-bottom: 18px;
    }

    &__link {
      font-size: 2vh;

      &:not(:last-child) {
        margin-bottom: 1.1vh;
      }
    }
  }

  @include h(624) {
    &__list {
      padding-top: 40px;
    }

    &__heading {
      margin-bottom: 10px;
    }

    &__group {
      padding-top: 15px;
      padding-bottom: 11px;
    }

    &__link {
      font-size: 13px;

      &:not(:last-child) {
        margin-bottom: 4px;
      }
    }
  }

  @include h(516) {
    &__list {
      padding-top: 60px;
    }

    &__link {
      font-size: 12px;

      &:not(:last-child) {
        margin-bottom: 3px;
      }
    }
  }
}

.transition-popup-enter-active {
  .the-menu__logo, .the-menu__close, .the-menu__list, .the-menu__link, .the-menu__lines {
    transition: 0.5s 0.5s;
    transition-property: transform, opacity;
    will-change: transform, opacity;
  }

  .the-menu__list {
    @for $i from 1 to $nav-items {
      .the-menu__link:nth-child(#{$i}) { transition-delay: $delay + $i * $delay-stagger; }
    }
  }
}

.transition-popup-enter {
  .the-menu__logo, .the-menu__close, .the-menu__list, .the-menu__link, .the-menu__lines {
    opacity: 0;
    transform: translateX(-50%);
  }
}

</style>



