<template>
  <div class="the-token-search-container">
    <div class="the-token-search-container__message">
      {{tokensToDisplay.length > 0 ? `Found ${tokensToDisplay.length}` : 'We found 0'}} Tokens
    </div>
    <div
      v-if="tokensToDisplay.length === 0"
      class="the-token-search-container__recomend"
    >
      We recomend you 3 most popular tokens
    </div>
    <div
      class="the-token-search-container__wrapper"
      v-if="tokensToDisplay.length > 0"
    >
      <div
        class="the-token-search-container__search-list"
        :class="mobile ? 'the-token-search-container__search-list_height' : ''"
      >
        <e-token-item
          v-for="item in tokensToDisplay"
          :key="item.contract"
          v-bind="item"
        />
      </div>
    </div>
    <div
      v-if="tokensToDisplay.length === 0"
      class="the-token-search-container__rec"
    >
      <e-token-item
        v-for="item in recommendedTokens"
        :key="item.contract"
        v-bind="item"
      />
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

import ETokenItem from '@/components/ETokenItem';

import { searchTokenMixin } from '@/util/mixins';

export default {
  name: 'the-token-search-container',

  props: {
    searchString: {
      type: String,
      default: '',
    },

    mobile: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    ...mapGetters({
      recommendedTokens: 'tokens/recommendedTokens',
    }),

    tokensToDisplay() {
      return this.$searchTokens(this.searchString).slice(0, 20);
    },
  },

  created() {
    this.$store.dispatch('tokens/loadTokens');
  },

  mixins: [searchTokenMixin],

  components: {
    ETokenItem,
  },
};
</script>


<style lang="scss">
@import '~@/scss/styles';

.the-token-search-container {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  width: calc(100% - 120px);
  padding: 0 30px;

  &__wrapper {
    display: flex;
  }

  &__search-list {
    max-height: 215px;
    width: 100%;
    overflow-y: auto;

    &::-webkit-scrollbar {
      width: 2px;
    }

    &::-webkit-scrollbar-track {
      background: $color-violet;
      border-radius: 2px;
    }

    &::-webkit-scrollbar-thumb {
      background: $color-blue;
      border-radius: 2px;
    }
  }

  &__search-list_height {
    max-height: 100%;
  }

  &__message {
    font-size: 14px;
    min-height: 44px;
    background-color: #f5f5f5;
    color: #000;
    font-weight: normal;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px 4px 0 0;
  }

  &__recomend {
    color: rgba(0, 0, 0, 0.4);
    font-weight: normal;
    font-size: 13px;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 50px;
    background-color: #f5f5f5;
  }
}
</style>



