<template>
  <div class="the-main-info">
    <div class="the-main-info__token the-main-info__token-polyswarm"><e-icon :src="iconPolyswarm" /></div>
    <div class="the-main-info__token the-main-info__token-nebulas"><e-icon :src="iconNebulas" /></div>
    <div class="the-main-info__token the-main-info__token-naga"><e-icon :src="iconNaga" /></div>
    <div class="the-main-info__token the-main-info__token-holo"><e-icon :src="iconHolo" /></div>
    <div class="the-main-info__token the-main-info__token-wpr"><e-icon :src="iconWpr" /></div>

    <div class="the-main-info__wrapper">
      <div class="the-main-info__label">DMEX — Decentralized Mercantile Exchange</div>
      <div class="the-main-info__heading">
        Enhance your <span class="the-main-info__highlight">Trading Experience</span>
      </div>
      <div class="the-main-info__description">
        We're working on a daily basis to upgrade your trading experience and keep it 100% safe.
      </div>
      <div class="the-main-info__buttons">
        <!-- ПОКА ЭТУ КНОПКУ НЕ ОТОБРАЖАЕМ -->
        <!-- <e-button
          fill="green-blue"
          fontWeight="500"
          fontSize="18"
          textTransform="none"
          padding="secondary"
        >
          Read our Story
        </e-button> -->

        <!-- BEGIN: Кнопки для залогиненного пользователя -->
        <template v-if="isLogin">
          <e-button
            fill="transparent-blue"
            fontWeight="500"
            fontSize="18"
            textTransform="none"
            padding="secondary"
            @click="handleMyWallet"
          >
            My Wallet
          </e-button>
        </template>
        <!-- END: Кнопки для залогиненного пользователя -->

        <!-- BEGIN: Кнопки для незалогиненого пользователя -->
        <template v-else>
          <e-button
            fill="transparent-blue"
            fontWeight="500"
            fontSize="18"
            textTransform="none"
            padding="secondary"
            @click="handleConnect"
          >
            Connect your wallet
          </e-button>
        </template>
        <!-- END: Кнопки для незалогиненого пользователя -->
      </div>
    </div>
  </div>
</template>

<script>
/**
 * Component: Секция "Enhance your Trading Experience" главной страницы
 *
 * @version 1.0.0
 */


import { mapGetters } from 'vuex';

import { POPUP_MY_WALLET, POPUP_IMPORT } from '@/store/constants';

import { routesMixin } from '@/util/mixins';

// Components
import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';


// Icons
import iconPolyswarm from '@/icons/e-icon__polyswarm.svg';
import iconNebulas from '@/icons/e-icon__nebulas.svg';
import iconNaga from '@/icons/e-icon__naga.svg';
import iconHolo from '@/icons/e-icon__holo.svg';
import iconWpr from '@/icons/e-icon__wpr.svg';

export default {
  name: 'the-main-info',

  data() {
    return {
      iconPolyswarm,
      iconNebulas,
      iconNaga,
      iconHolo,
      iconWpr,
    };
  },

  computed: {
    ...mapGetters({
      isLogin: 'wallets/isLogin',
    }),
  },

  created() {
    this.$store.dispatch('wallets/loadPrivateWallets');
  },

  methods: {
    /**
     * Открытие страницы импорт кошелька
     */
    handleConnect() {
      this.$router.push(this.$popupRoute(POPUP_IMPORT));
    },

    /**
     * Открытие страницы моего кошелька
     */
    handleMyWallet() {
      this.$router.push(this.$popupRoute(POPUP_MY_WALLET));
    },
  },

  components: {
    EButton,
    EIcon,
  },

  mixins: [routesMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-main-info {
  $this: &;

  padding: 90px 0;
  background: #071566;
  position: relative;

  @include r(767) {
    padding: 60px 0;
  }

  &__wrapper {
    width: 100%;
    max-width: 610px;
    padding: 0 30px;
    box-sizing: border-box;
    margin: 0 auto;
    text-align: center;
  }

  &__label {
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 500;
    letter-spacing: .5px;
    color: #fff;
    opacity: .42;
    padding-bottom: 16px;
  }

  &__heading {
    font-size: 36px;
    font-weight: 500;
    letter-spacing: -1.5px;
    line-height: 61px;
    padding-bottom: 22px;
    text-align: center;
    color: #fff;

    @include r(640) {
      line-height: 47px;
      padding-bottom: 2px;
    }
  }

  &__highlight {
    color: #00E997;
  }

  &__description {
    font-size: 19px;
    font-weight: normal;
    line-height: 29px;
    padding-bottom: 30px;
    color: #fff;

    @include r(640) {
      font-size: 18px;
      font-weight: 300;
    }
  }

  &__buttons {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin-bottom: -20px;

    .e-button {
      margin: 0 10px 20px;
    }
  }

  &__token {
    position: absolute;
  }

  &__token-polyswarm {
    width: 180px;
    height: 181px;
    left: calc(50% + 400px);
    top: -33px;
    animation: pulse 7s linear infinite;
    animation-delay: 2s;
  }

  &__token-nebulas {
    width: 176px;
    height: 176px;
    left: calc(50% - 660px);
    top: -90px;
    animation: pulse 5s linear infinite;
  }

  &__token-naga {
    width: 80px;
    height: 80px;
    left: calc(50% - 400px);
    top: 72px;
    animation: pulseMax 6s ease-in-out infinite;
  }

  &__token-holo {
    width: 102px;
    height: 102px;
    left: calc(50% - 620px);
    top: 260px;
    animation: pulseMax 8s ease-in-out infinite;
  }

  &__token-wpr {
    width: 80px;
    height: 80px;
    left: calc(50% + 330px);
    top: 290px;
    animation: pulseMax 5s ease-in-out infinite;
  }

  @keyframes pulse {
    0% { transform: scale(1); }
    50% { transform: scale(1.15); }
    100% { transform: scale(1); }
  }

  @keyframes pulseMax {
    0% { transform: scale(1); }
    50% { transform: scale(1.35); }
    100% { transform: scale(1); }
  }
}
</style>



