<template>
  <div
    class="e-token-item"
    @click="onClick"
  >
    <div class="e-token-item__body">
      <div class="e-token-item__token-shortname">{{shortName}}</div>
      <div class="e-token-item__token-name">{{name}}</div>
      <div class="e-token-item__token-link">https://minerone.io</div>
    </div>
  </div>
</template>

<script>
import { BigNumber } from 'bignumber.js';

import iconProfit from '@/icons/e-icon_profit.svg';
import iconLoss from '@/icons/e-icon_loss.svg';
import iconEarned from '@/icons/e-icon_earned.svg';

import ETokenIcon from '@/components/ETokenIcon';
import EIcon from '@/components/EIcon';
import EBigNumber from '@/components/EBigNumber';
import ESmallPriceChart from '@/components/ESmallPriceChart';
import ESvgFromStore from '@/components/ESvgFromStore';

export default {
  name: 'e-token-item',
  inheritAttrs: false,

  props: {
    catalogSlide: {
      type: Boolean,
      default: false,
    },

    contract: {
      type: String,
      required: true,
    },

    // Название токена
    name: {
      type: String,
      default: '',
    },

    // Сокращенное название токена
    shortName: {
      type: String,
      default: '',
    },

    decimals: {
      type: Number,
      default: 0,
    },

    // Общее количество токена
    total: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    // Price
    price: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    priceDiff24InPercent: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    increasing24: {
      type: Boolean,
      default: true,
    },

    // Является ли токен выбраным "моим"
    isMy: {
      type: Boolean,
      default: false,
    },

    // Массив с данными для отрисовки графика изменения цен
    candles24h: {
      type: Array,
      default: null,
    },

    // Является ли текущим выбранным токеном
    active: {
      type: Boolean,
      default: false,
    },

    websiteUrl: {
      type: String,
      default: '#',
    },
  },

  created() {
    Object.assign(this, {
      iconProfit,
      iconLoss,
      iconEarned,
    });
  },

  methods: {
    async onClick() {
      this.$router.push({ name: 'token', params: { token: this.contract } });
    },
  },

  components: {
    ETokenIcon,
    EIcon,
    EBigNumber,
    ESmallPriceChart,
    ESvgFromStore,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-token-item {
  $this: &;

  background-color: #fff;
  padding: 0 20px;
  display: flex;
  cursor: pointer;
  user-select: none;
  transition: $transition-time;

  &:last-child {
    border-radius: 0 0 4px 4px;
  }

  &:not(:last-child) {
    .e-token-item__body {
      border-bottom: 2px solid #fafafa;
    }
  }

  &:hover {
    background-color: #f5f5f5;
  }

  @include r(374) {
    padding-left: 10px;
  }

  &__head {
    flex-basis: 40%;
    min-width: 40%;
    display: flex;

    @include r(768) {
      flex-basis: 33%;
      min-width: 33%;
    }

    @include r(374) {
      flex-basis: 27%;
      min-width: 27%;
    }
  }

  &__center {
    flex-basis: 30%;
    min-width: 30%;
    display: flex;
    position: relative;
    padding-left: 20px;
    box-sizing: border-box;

    @include r(768) {
      min-width: 37%;
      flex-basis: 37%;
    }

    @include r(374) {
      flex-basis: 43%;
      min-width: 43%;
      padding-left: 15px;
    }
  }

  &__body {
    box-sizing: border-box;
    padding-top: 6px;
    width: 100%;
  }

  &__token-shortname {
   font-size: 12px;
   color: #071050;
   line-height: 17px;
   font-weight: 600;

    @include r(374) {
      font-size: 14px;
    }
  }

  &__token-name {
    font-size: 12px;
    font-weight: normal;
    color: rgba(0, 0, 0, 0.6);
    letter-spacing: .6px;
    line-height: 1;
    white-space: nowrap;
    overflow: hidden;

    @include r(480) {
      max-width: 60px;
    }

    @include r(374) {
      font-size: 10px;
    }
  }

  &__token-link {
    color: #1f2ee9;
  }

  &__amount-icon {
    min-width: 16px;
    min-height: 17px;
    max-width: 16px;
    max-height: 17px;
    position: absolute;
    top: 50%;
    left: 6px;
    margin-top: -8px;

    @include r(374) {
      left: 10px;
    }
  }

  &__amount-eth {
    width: 9px;
    height: 8px;
    display: inline-block;

    .e-icon svg {
      fill: $color-blue;
    }
  }

  &__amount-icon_profit {
    transform: rotate(-90deg);
  }

  &__amount {
    line-height: 1;
    .e-big-number {
      font-size: 11px;
      font-weight: 600;
      letter-spacing: .73px;

      &__int, &__leading-zeros, &__separator {
        color: #9A9EA7;
      }

      &__fraction {
        color: $color-ultramarine;
      }
    }
  }

  &__diff {
    display: inline-block;
    border: 1px solid #E3F2E8;
    border-radius: 8px;
    color: $color-green;
    padding: 3px 4px 1px;
    letter-spacing: 0;
    line-height: 1;
    font-size: 9px;
  }

  &__graph-wrapper {
    width: 90px;
  }
}
</style>



