<template>
  <div
    class="e-futures-item-detail"
    :class="{
      'e-futures-item-detail__light': theme === 'light',
      'e-futures-item-detail__dark': theme === 'dark'
    }"
    @click="handleClick"
  >
    <div class="the-header-futures-result__search-table-item-block">
      <div>
        <div class="e-futures-item-detail__name">{{asset}}</div>
      </div>
    </div>

    <div class="the-header-futures-result__search-table-item-block">
      <e-big-number :value="markPrice" :decimals="FUTURES_PRICE_DECIMALS" :decimalPlaces="2" v-if="markPrice && !markPrice.isZero()" />
      <span v-if="markPrice && !markPrice.isZero()" class="e-futures-item-detail__search-table-item-sign">$</span>
      <span v-else class="e-futures-item-detail__placeholder"></span>
    </div>
    <div class="the-header-futures-result__search-table-item-block">
      <e-big-number :value="volume24h" :decimals="FUTURES_AMOUNT_DECIMALS" :decimalPlaces="2" />

      <span class="e-futures-item-detail__search-table-item-currency">&nbsp;USD</span>
    </div>
    <div class="the-header-futures-result__search-table-item-block">
      <div class="e-futures-item-detail__leverage-value">
        <div class="e-futures-item-detail__leverage-value-item -increase">
          3X
        </div>
        <div class="e-futures-item-detail__leverage-value-item -increase">
          5X
        </div>
        <div class="e-futures-item-detail__leverage-value-item -increase">
          10X
        </div>
        <!--<div class="e-futures-item-detail__leverage-value-item -decrease">-->
          <!--<e-big-number :value="leverage.sell" :decimal-places="1"/>X-->
        <!--</div>-->
      </div>
    </div>

    <div class="the-header-futures-result__search-table-item-block e-futures-item-detail__date">
      <div class="e-futures-item-detail__expires-value">
        <div class="e-futures-item-detail__expires-value-item">1D</div>
        <div class="e-futures-item-detail__expires-value-item">3D</div>
        <div class="e-futures-item-detail__expires-value-item">7D</div>
      </div>
    </div>

    <div class="the-header-futures-result__search-table-item-block">
      <span
        class="e-futures-item-detail__change"
        :class="isTradePriceIncreasing ? 'e-futures-item-detail__change-increase' : 'e-futures-item-detail__change-decrease'"
      >
        <e-big-number
          :value="tradePriceDiffVal"
          :decimals="9"
          :decimal-places="2"
          sign
        />%
      </span>
    </div>
  </div>
</template>

<script>
import { BigNumber } from 'bignumber.js';
import { mapGetters } from 'vuex';
import moment from 'moment';

import iconVerified from '@/icons/e-icon__verified.svg';
import iconUrl from '@/icons/e-icon__url.svg';
import iconEth from '@/icons/e-icon_earned.svg';

import ETokenIcon from '@/components/ETokenIcon';
import EIcon from '@/components/EIcon';
import EBigNumber from '@/components/EBigNumber';
import ESmallPriceChart from '@/components/ESmallPriceChart';
import ESvgFromStore from '@/components/ESvgFromStore';

const toShortenedAddress = a => `${a.slice(0, 12)}…${a.slice(-4)}`;

export default {
  name: 'e-futures-item-detail',
  inheritAttrs: false,

  props: {
    asset: {
      type: String,
      required: true,
    },

    baseToken: {
      type: String,
      required: true,
    },

    contractAddress: {
      type: String,
      required: true,
    },

    // Название токена
    name: {
      type: String,
      // required: true,
    },

    // Сокращенное название токена
    shortName: {
      type: String,
      // required: true,
    },

    websiteUrl: {
      type: String,
      default: null,
    },

    isWebsiteVerified: {
      type: Boolean,
      default: false,
    },

    increasing24: {
      type: Boolean,
      default: false,
    },

    tradeLastPrice: {
      type: BigNumber,
    },

    markPrice: {
      type: BigNumber,
    },

    floorPrice: {
      type: BigNumber,
    },

    capPrice: {
      type: BigNumber,
    },

    tradePriceDiffVal: {
      type: BigNumber,
    },

    isTradePriceIncreasing: {
      type: Boolean,
    },

    leverage: {
      type: Object,
    },

    expiresApproxAt: {
      type: Date,
    },

    candles24h: {
      type: Array,
      default: null,
    },

    volume24h: {
      type: BigNumber,
    },
  },

  computed: {
    shortenedAddress: vm => toShortenedAddress(vm.contract),

    expiresApproxAtDate() {
      return this.expiresApproxAt ? moment(this.expiresApproxAt).format('MMM DD, YYYY') : null;
    },

    ...mapGetters({
      tokenByContract: 'tokens/tokenByContract',
      theme: 'ui/getTheme',
    }),
  },

  icons: {
    iconEth,
    iconVerified,
    iconUrl,
  },

  methods: {
    handleClick() {
      this.$emit('click', { contract: this.contract, name: this.name, shortName: this.shortName });
    },
  },

  components: {
    ETokenIcon,
    EIcon,
    EBigNumber,
    ESmallPriceChart,
    ESvgFromStore,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-futures-item-detail {
  $this: &;
  cursor: pointer;
  user-select: none;
  display: flex;
  align-items: stretch;
  height: 29px;
  overflow: hidden;
  font-size: 10px;

  .the-header-futures-result__search-table-item-block {
    background-color: #fff;
    border-bottom: 1px solid #e7e8eb;
    transition: $transition-time;
  }

  &:last-child {
    .the-header-futures-result__search-table-item-block {
      border-bottom: 0;
    }
  }

  &:hover {
    .the-header-futures-result__search-table-item-block {
      background: #F3F6FF;
    }

    #{$this}__name {
      color: #1f2ee9;
    }

    #{$this}__address {
      color: rgba(31, 46, 233, 0.4);
    }

    #{$this}__symbol {
      color: #1F2EE9;
    }
  }

  &__search-table-item-sign {
    font-weight: 600;
    padding-left: 2px;
    color: #0D105B;
  }

  &__token-img {
    margin-right: 7px;
  }

  .e-tocken-icon {
    width: 18px;
    height: 18px;
    position: relative;

    &:before {
      content: '';
      display: block;
      background: #fff;
      border-radius: 50%;
      position: absolute;
      z-index: 0;
      width: 16px;
      height: 16px;
      top: 1px;
      left: 1px;
    }

    img {
      position: relative;
      z-index: 1;
    }

    .e-svg-from-store__preloader {
      width: 18px;
      height: 18px;
    }
  }

  &__name {
    max-width: 71px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 10px;
    font-weight: 500;
    color: #071050;
    letter-spacing: 0;
    line-height: 10px;
    transition: color $transition-time;
  }

  &__token-short-name {
    max-width: 71px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 10px;
    line-height: 11px;
    font-weight: 500;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    color: #071050;
  }

  &__address {
    font-size: 10px;
    color: rgba(0, 0, 0, 0.4);
    letter-spacing: 0;
    line-height: 1.7;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin: -2px 0 0 0;
    transition: color $transition-time;
  }

  &__url {
    font-size: 10px;
    letter-spacing: 0;
    line-height: 1.7;
    color: #1F2EE9;
    margin: -5px 0 0 0;
  }

  &__info {
    flex: 0 0 auto;
    display: flex;
    flex-flow: column;
  }

  &__extended-info {
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    overflow-x: hidden;
    max-width: 70px;
  }

  &__verified {
    width: 8px;
    height: 9px;
    transform: rotate(135deg);
    margin-left: 3px;

    svg {
      width: 8px;
      height: 9px;
    }
  }

  &__symbol {
    font-size: 14px;
    font-weight: 400;
    color: rgba(0, 0, 0, 0.4);
    letter-spacing: 0;
    line-height: (17 / 14);
    margin-left: 4px;
    transition: color $transition-time;
    overflow-x: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  &__shortened-address {
    display: flex;
    align-items: center;
    color: #1F2EE9;
    font-size: 10px;
  }

  &__icon-url {
    width: 6px;
    height: 6px;
    margin-left: 4px;
  }

  .e-big-number {
    font-size: 10px;
    color: $color-dark-blue;
    font-weight: 700;

    &__fraction {
      margin-left: 1px;
    }

    &__separator {
      margin-left: 1px;
    }

    &__leading-zeros,
    &__trailing-zeros {
      opacity: 0.5;
    }
  }

  &__search-table-item-currency {
    font-size: 10px;
    color: $color-dark-blue;
    font-weight: 700;
  }

  &__icon-eth {
    width: 10px;
    height: 10px;
    margin-left: 4px;

    svg {
      fill: $color-dark-blue;
    }
  }

  &__change {
    font-size: 9px;
    font-weight: 600;
  }

  &__change-increase {
    color: #1FCE43;

    .e-big-number {
      color: #1FCE43;
      opacity: 1;
    }
  }

  &__change-decrease {
    color: #DA4830;

    .e-big-number {
      color: #DA4830;
      opacity: 1;
    }
  }

  &__placeholder {
    display: block;
    width: 50px;
    height: 8px;
    border-radius: 20px;
    opacity: 1;
    background: #E3E3EE;
  }

  &__leverage-value {
    display: flex;
    justify-content: flex-end;
  }

  &__leverage-value-item {
    text-transform: uppercase;
    font-weight: 600;
    width: 25px;
    height: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 1px;

    &:first-child {
      border-radius: 2px 0 0 2px;
    }

    &:last-child {
      border-radius: 0 2px 2px 0;
    }

    &.-increase {
      color: #fff;
      background: #0097FE;

      .e-big-number,
      .e-big-number__trailing-zeros,
      .e-big-number__sign,
      .e-big-number__separator,
      .e-big-number__leading-zeros {
        color: #fff;
        opacity: 1;
      }
    }

    &.-decrease {
      color: #DA4830;
      background: rgba(220,71,39,0.10);

      .e-big-number,
      .e-big-number__trailing-zeros,
      .e-big-number__sign,
      .e-big-number__separator,
      .e-big-number__leading-zeros {
        color: #DA4830;
        opacity: 1;
      }
    }
  }

  &__expires-value {
    display: flex;
    justify-content: flex-end;
  }

  &__expires-value-item {
    text-transform: uppercase;
    font-weight: 600;
    width: 25px;
    height: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 1px;
    color: #8D9098;
    background: rgba(1,34,231,0.10);

    &:first-child {
      border-radius: 2px 0 0 2px;
    }

    &:last-child {
      border-radius: 0 2px 2px 0;
    }
  }

  &__dark {
    .the-header-futures-result__search-table-item-block {
      background-color: rgba(255,255,255,.025);
      border-bottom: 0;

      &:first-child {
        border-radius: 3px 0 0 3px;
      }

      &:last-child {
        border-radius: 0 3px 3px 0;
      }
    }

    &:hover {
      .the-header-futures-result__search-table-item-block {
        background-color: rgba(255,255,255,.04);
      }

      #{$this}__name {
        color: #fff;
      }

      #{$this}__address,
      #{$this}__symbol {
        color: #5C6062;
      }
    }

    #{$this}__date {
      color: rgba(#8D9098, .5);
    }

    #{$this}__search-table-item-sign {
      color: #8D9098;
    }

    #{$this}__name {
      color: #fff;
    }

    #{$this}__token-short-name,
    #{$this}__symbol {
      color: #fff;
    }

    #{$this}__address {
      color: #fff;
    }

    #{$this}__url,
    #{$this}__shortened-address {
      color: #0098FF;
    }

    .e-big-number {
      color: #fff;
    }

    #{$this}__change {
      .e-big-number,
      .e-big-number__leading-zeros,
      .e-big-number__trailing-zeros {
        opacity: 1;
      }
    }

    #{$this}__change-increase {
      color: #1FCE43;

      .e-big-number {
        color: #1FCE43;
      }
    }

    #{$this}__change-decrease {
      color: #DA4830;

      .e-big-number {
        color: #DA4830;
      }
    }

    #{$this}__icon-eth {
      svg {
        fill: #fff;
      }
    }

    #{$this}__placeholder {
      opacity: 0.9;
      background: rgba(92,96,98,0.30);
    }

    #{$this}__search-table-item-currency {
      color: #fff;
    }

    #{$this}__expires-value-item {
      background: #343E4E;
      color: #fff;
    }
  }
}
</style>



