      <!--</div>-->
    <!--</div>-->
  </router-link>
</template>

<script>
import { mapState, mapGetters } from 'vuex';

import iconLogo from '@/icons/e-icon__dmex-logo.svg';
import iconDottedAnimation from '@/icons/e-icon_dotted-animation.svg';

import EIcon from '@/components/EIcon';
import EBigNumber from '@/components/EBigNumber';

export default {
  name: 'the-logo-inner',

  props: {
    withVolume: {
      type: Boolean,
      default: false,
    },

    withVersion: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    withoutText() {
      const withText = this.withVolume || this.withVersion;
      return !withText;
    },

    ...mapState({
      volumeTotal: state => state.trades.volumeTotal,
    }),
    ...mapGetters({
      isVolumesInitSuccess: 'trades/isVolumesInitSuccess',
      isVolumesInitError: 'trades/isVolumesInitError',
    }),
  },

  created() {
    if (this.withVolume) {
      this.$store.dispatch('trades/loadVolumes');
      this.$store.dispatch('trades/listenToTradeVolumesChange');
    }
  },

  beforeDestroy() {
    if (this.withVolume) {
      this.$store.dispatch('trades/stopListeningToTradeVolumesChanges');
    }
  },

  icons: {
    iconLogo,
    iconDottedAnimation,
  },

  components: {
    EIcon,
    EBigNumber,
  },
};
</script>

