  &__dmex {
    width: 66px;
    height: 29px;
    margin-right: 12px;

    svg path {
      transition: fill .15s ease-in-out;
    }
  }

  &__vol {
    font-size: 10px;
    font-weight: 600;
    color: #AFB8BE;
  }

  &__vol-label {
    white-space: nowrap;
  }

  &__body {
    padding-left: 44px;
    padding-top: 4px;
    font-weight: 600;
    color: #0D105B;

    &_without-text {
      padding-top: 12px;
    }
  }

  &__text {
    font-size: 24px;
    font-weight: 600;
    line-height: 1;
    transition: color .15s ease-in-out;

    @include r(1460) {
      font-size: 23px;
    }
  }

  &__icon-pending {
    width: 24px;
    height: 3px;
    margin: 6px 0 0 2px;
  }

  &__version {
    font-size: 10px;
    font-weight: 600;
    color: #AFB8BE;
    transition: color .15s ease-in-out;
  }
}

