<template>
  <div class="the-socials">
    <div class="the-socials__item">
      <a href="mailto:contact@dmex.app" target="_blank" class="the-socials__wrapper">
        <div class="the-socials__icon">
          <e-icon :src="iconLetter"/>
        </div>
        <div class="the-socials__title">E-mail</div>
        <div class="the-socials__link">Contact@Dmex.App</div>
      </a>
    </div>
    <div class="the-socials__item">
      <a href="https://t.me/dmex_app" target="_blank" class="the-socials__wrapper">
        <div class="the-socials__icon">
          <e-icon :src="iconTelegram"/>
        </div>
        <div class="the-socials__title">Telegram</div>
        <div class="the-socials__link">@Dmex_App</div>
      </a>
    </div>
    <div class="the-socials__item">
      <a href="https://twitter.com/DMEX_APP" target="_blank" class="the-socials__wrapper">
      <div class="the-socials__icon">
        <e-icon :src="iconTwitter"/>
      </div>
      <div class="the-socials__title">Twitter</div>
      <div class="the-socials__link">@Dmex_App</div>
      </a>
    </div>
  </div>
</template>

<script>
import iconReddit from '@/icons/e-icon_reddit.svg';
import iconTwitter from '@/icons/e-icon_twitter.svg';
import iconLetter from '@/icons/e-icon_letter.svg';
import iconTelegram from '@/icons/e-icon_telegram.svg';

import EIcon from '@/components/EIcon';

export default {
  name: 'the-socials',

  data() {
    return {
      iconLetter,
      iconTwitter,
      iconReddit,
      iconTelegram,
    };
  },

  components: {
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-socials {
  $this: &;

  display: flex;
  justify-content: center;
  font-weight: 500;

  @include r(768) {
    flex-wrap: wrap;
  }

  &__item {
    flex-basis: 25%;
    min-width: 25%;
    display: flex;
    align-items: center;
    flex-direction: column;

    @include r(768) {
      flex-basis: 50%;
      min-width: 50%;
      margin-bottom: 28px;
    }
  }

  &__icon {
    height: 50px;
    width: 50px;
    border-radius: 50%;
    box-shadow: 0 12px 24px rgba(#94A5B4, .14);
    margin-bottom: 18px;
    transform: scale(1);
    transition: $transition-time;
    position: relative;

    .e-icon svg {
      transform: translate3d(0,0,0);
    }

    @include r(768) {
      margin-bottom: 20px;
    }

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      opacity: 0;
      border-radius: 50%;
      background-color: rgba(black, .1);
      transition: $transition-time;
    }
  }

  &__title {
    font-size: 16px;
    color: $color-dark-blue;
    font-weight: 500;
    line-height: 1;
    padding-bottom: 4px;
    transition: all .2s ease-in-out;

    @include r(374) {
      font-size: 14px;
    }
  }

  &__link {
    color: $color-blue;
    font-weight: 500;
    font-size: 12px;
    text-decoration: none;
    position: relative;
    padding-bottom: 2px;
    overflow: hidden;
    transition: all .2s ease-in-out;

    @include r(374) {
      font-size: 12px;
    }

    &::before {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 2px;
      background: #1F2EE9;
      opacity: .2;
    }

    &::after {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 2px;
      background: #1F2EE9;
      opacity: .2;
      transform: translate(-100%, 0);
      transition: all .3s ease-in-out;
    }
  }

  &__wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
    text-decoration: none;

    &:hover {
      color: #333;

      #{$this}__icon {
        transform: scale(1.08);

        &:before {
          opacity: 1;
        }
      }

      #{$this}__title {
        color: #333;
      }

      #{$this}__link {
        color: #333;

        &::after {
          transform: translate(0, 0);
          opacity: 1;
        }
      }
    }
  }
}
</style>



