<template>
  <div class="the-header-new">
    <router-link
      :to="{ name: 'index' }"
      class="the-header-new__main-logo"
    >
      <e-icon :src="iconLogoMain" />
    </router-link>

    <div class="the-header-new__main-menu">
      <router-link
        :to="{ name: 'index' }"
        class="the-header-new__main-menu-item"
      >
        Homepage
      </router-link>

      <router-link
        :to="{ name: 'futures' }"
        class="the-header-new__main-menu-item"
      >
        Trading
      </router-link>

      <router-link
        :to="{ name: 'news' }"
        :class="{
          'router-link-exact-active': ['postfirst', 'postsecond', 'postthird', 'nomorelistings', 'tradingview', 'futures-contract', 'nomorelistings', 'tradingview', 'dark-mode', 'minimum-order']
          .includes($route.name)
        }"
        class="the-header-new__main-menu-item"
      >
        News
      </router-link>

      <router-link
        :to="{ name: 'faq' }"
        :class="{ 'router-link-exact-active': ['faq'].includes($route.name) }"
        class="the-header-new__main-menu-item"
      >
        FAQ
      </router-link>

      <router-link
        :to="{ name: 'aboutus' }"
        :class="{ 'router-link-exact-active': ['aboutus', 'aboutethermium', 'aboutdecenralization', 'fees'].includes($route.name) }"
        class="the-header-new__main-menu-item"
      >
        About
      </router-link>

      <a href="https://github.com/dmex-app/help-docs" class="the-header-new__main-menu-item" target="_blank">
        Help
      </a>


      <router-link
        :to="{ name: 'support' }"
        class="the-header-new__main-menu-item"
      >
        Contacts
      </router-link>
    </div>

    <div class="the-header-new__main-connect-buttons">
      <!-- BEGIN: Кнопки для залогиненного пользователя -->
      <template v-if="isLogin">
        <e-button
          fill="transparent-blue"
          size="s"
          fontWeight="500"
          @click="handleMyWallet"
        >
          My Wallet
        </e-button>
      </template>
      <!-- END: Кнопки для залогиненного пользователя -->

      <!-- BEGIN: Кнопки для незалогиненого пользователя -->
      <template v-else>
        <e-button
          fill="transparent-blue"
          size="s"
          fontWeight="500"
          @click="handleConnect"
        >
          Connect
        </e-button>

        <div class="the-header-new__main-connect-buttons-separator">or</div>

        <e-button
          fill="green-blue"
          size="s"
          fontWeight="500"
          @click="handleGenerate"
        >
          Create a new wallet
        </e-button>
      </template>
      <!-- END: Кнопки для незалогиненого пользователя -->
    </div>

    <div class="the-header-new__burger" @click="toggleMenu">
      <div class="the-header-new__burger-separator"/>
    </div>

    <transition name="mobile-menu">
      <div class="the-header-new__adaptive" v-if="isMenuOpen" :class="{ 'is-open': isMenuOpen }">
        <div class="the-header-new__adaptive-wrapper" v-click-outside="toggleMenu">

          <router-link
            :to="{ name: 'index' }"
            class="the-header-new__main-menu-adaptive-item"
            @click.native="isMenuOpen = false"
          >Homepage
          </router-link>
          <router-link
            :to="{ name: 'tokens' }"
            class="the-header-new__main-menu-adaptive-item"
          >
            Exchange
          </router-link>

          <router-link
            :to="{ name: 'news' }"
            :class="{ 'router-link-exact-active': ['postfirst', 'postsecond', 'postthird'].includes($route.name) }"
            class="the-header-new__main-menu-adaptive-item"
          >News
          </router-link>

          <router-link
            :to="{ name: 'aboutus' }"
            :class="{ 'router-link-exact-active': ['aboutus', 'aboutethermium', 'aboutdecenralization', 'fees'].includes($route.name) }"
            class="the-header-new__main-menu-adaptive-item"
          >About
          </router-link>

          <router-link
            :to="$popupRoute(POPUP_HELP)"
            class="the-header-new__main-menu-adaptive-item"
          >
            Help
          </router-link>
          <router-link
            :to="$popupRoute(POPUP_SUPPORT)"
            class="the-header-new__main-menu-adaptive-item"
          >
            Contacts
          </router-link>
          <!--<router-link-->
            <!--:to="$popupRoute(POPUP_LIST_TOKEN)"-->
            <!--class="the-header-new__main-menu-adaptive-item"-->
          <!--&gt;-->
            <!--List token-->
          <!--</router-link>-->
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
/**
 * Component: Заголовок главной страницы
 *
 * @version 2.0.0
 */

import { mapGetters } from 'vuex';

import { POPUP_WALLET, POPUP_MY_WALLET, POPUP_IMPORT } from '@/store/constants';
import { routesMixin } from '@/util/mixins';

// Components
import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';

// Icons
import iconLogoMain from '@/icons/e-icon__dmex-logo.svg';

export default {
  name: 'the-header-new',

  data() {
    return {
      iconLogoMain,
      isMenuOpen: false,
    };
  },

  computed: {
    ...mapGetters({
      isLogin: 'wallets/isLogin',
      firstContractHash: 'futuresActiveContract/firstContractHash',
    }),
  },

  created() {
    this.$store.dispatch('wallets/loadPrivateWallets');
  },

  methods: {
    toggleMenu() {
      this.isMenuOpen = !this.isMenuOpen;
    },

    /**
     * Генерация нового кошелька
     */
    handleGenerate() {
      this.$router.push(this.$popupRoute(POPUP_WALLET));
    },

    /**
     * Открытие страницы импорт кошелька
     */
    handleConnect() {
      this.$router.push(this.$popupRoute(POPUP_IMPORT));
    },

    /**
     * Открытие страницы моего кошелька
     */
    handleMyWallet() {
      this.$router.push(this.$popupRoute(POPUP_MY_WALLET));
    },
  },

  components: {
    EButton,
    EIcon,
  },

  mixins: [routesMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-header-new {
  $this: &;

  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 30px 60px;
  height: 120px;
  margin: 0 auto;
  box-sizing: border-box;
  position: relative;
  z-index: 9;

  @include r(1280) {
    padding: 30px;
  }

  @include r(640) {
    padding: 30px 15px;
  }

  &__burger {
    position: relative;
    width: 32px;
    height: 19px;
    display: none;
    cursor: pointer;

    @include r(992) {
      display: block;
    }

    &:before, &:after, #{$this}__burger-separator {
      position: absolute;
      left: 0;
      right: 0;
      background-color: #00e997;;
      height: 2px;
    }

    &:before, &:after {
      content: '';
    }

    &:before {
      top: 0;
    }

    &:after {
      bottom: 0;
    }

    #{$this}__burger-separator {
      top: 8px;
    }

    @include r(640) {
      position: absolute;
      right: 15px;
      top: 30px;
    }
  }

  &__main-logo {
    width: 99px;
    height: 44px;

    svg {
      .e-logo-dmex__white-text {
        fill: #fff;
      }
    }

    @include r(640) {
      width: 100%;
      height: 55px;
      display: flex;
      margin-top: -10px;

      .e-icon {
        width: 108px;
      }
    }
  }

  &__main-menu {
    display: flex;
    align-items: center;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);

    @include r(992) {
      display: none;
    }
  }

  &__main-connect-buttons-separator {
    color: rgba(129, 168, 247, .5);
    padding: 0 10px;
    text-transform: uppercase;
    font-size: 10px;
  }

  &__main-menu-item,
  &__main-menu-adaptive-item {
    display: block;
    font-family: 'DIN Pro';
    font-size: 12px;
    font-weight: 500;
    color: #00e997;
    opacity: .5;
    background: transparent;
    border-radius: 16px;
    transition: opacity .15s ease-in-out;
    padding: 7px 15px;
    box-sizing: border-box;
    text-decoration: none;
    position: relative;
    text-transform: uppercase;

    &:hover {
      opacity: 1;
    }

    &.router-link-exact-active {
      opacity: 1;
      color: #fff;
      background-color: rgba(0, 115, 233, 0.1);
    }

    @include r(1140) {
      font-size: 13px;
      padding: 0 10px;
    }
  }

  &__main-menu-adaptive-item {
    margin-bottom: 30px;

    @include r(1140) {
      font-size: 15px;
      padding: 4px 10px;
    }
  }

  &__main-connect-buttons {
    display: flex;
    align-items: center;

    @include r(992) {
      margin-left: auto;
      margin-right: 30px;
    }

    @include r(640) {
      display: none;
    }

    @include r(450) {
      margin-left: 0;
      justify-content: flex-end;
    }
  }

  &__adaptive {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9990;
    justify-content: center;
    align-items: center;
    background: rgba(2, 3, 27, 0.5);
  }

  &__adaptive-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    right: 65px;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: radial-gradient(ellipse at center, #01106f 0%, #01106f 70%, #030a47 100%);
  }

  .mobile-menu-enter-active, .mobile-menu-leave-active {
    transition: opacity .5s;

    #{$this}__adaptive-wrapper {
      transition: transform .5s ease;
    }
  }

  .mobile-menu-enter, .mobile-menu-leave-to {
    opacity: 0;

    #{$this}__adaptive-wrapper {
      transform: translateX(-100%);
    }
  }
}

</style>



