<template>
  <div class="the-token-search-etherscan-mobile">
    <div class="the-token-search-etherscan-mobile__head">
      <div class="the-token-search-etherscan-mobile__label">
        search token
      </div>
      <div class="the-token-search-etherscan-mobile__input">
        <input
          v-focus="true"
          v-model="searchString"
          type="text"
          class="the-token-search-etherscan-mobile__field"
          placeholder="Search by name or address"
        >
        <div
          class="the-token-search-etherscan-mobile__close"
          @click="handleClose"
        >
          <e-icon :src="$options.icons.iconClose"/>
        </div>
      </div>
    </div>

    <the-token-search-result-new
      class="the-token-search-etherscan-mobile__result"
      v-if="searchString !== ''"
      :searchString="searchString"
      :mobile="true"
      @select="handleTokenSelect"
    />
  </div>
</template>

<script>
import { mixin as focusMixin } from 'vue-focus';

import iconClose from '@/icons/e-icon_round-close.svg';

import TheTokenSearchResultNew from '@/containers/blocks/TheTokenSearchResultNew';
import EIcon from '@/components/EIcon';

export default {
  name: 'the-token-search-etherscan-mobile',

  props: {
    value: {
      type: String,
      default: '',
    },
  },

  computed: {
    searchString: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit('input', value);
      },
    },
  },

  icons: {
    iconClose,
  },

  methods: {
    handleTokenSelect(item) {
      this.$router.push({ name: 'token', params: { token: item.contract } });
    },

    handleClose() {
      this.searchString = '';
      this.$emit('close');
    },
  },

  components: {
    TheTokenSearchResultNew,
    EIcon,
  },

  mixins: [focusMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-token-search-etherscan-mobile {
  $this: &;
  $input-padding-bottom: 6px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  background-image: radial-gradient(#001DC3 0%, #030D53 53%, #02010A 100%);
  display: flex;
  flex-flow: column;

  &__head {
    flex: 0 0 auto;
    height: 98px;
    padding: 20px 62px 17px 31px;
    width: 100%;
    display: flex;
    box-sizing: border-box;
    flex-flow: column;
    justify-content: space-between;
  }

  // specificity wars
  #{$this}__result {
    $margin-right: 62px;
    $margin-left: 31px;

    flex: 1;
    width: calc(100% - #{$margin-right} - #{$margin-left});
    height: unset;
    box-sizing: border-box;
    margin: 0 $margin-right 8px $margin-left;
  }

  &__label {
    text-transform: uppercase;
    color: rgba(129, 168, 247, 0.5);
    font-size: 10px;
    font-weight: 500;
    line-height: 16px;
    letter-spacing: 0.5px;
  }

  &__input {
    position: relative;
    border-bottom: 2px solid #00e997;
    padding-bottom: $input-padding-bottom;
    box-sizing: border-box;
    display: flex;
    flex-flow: column;
    justify-content: flex-end;
    padding-left: 0;
    font-size: 23px;
    height: 25px;
  }

  &__field {
    width: 100%;
    background-color: transparent;
    color: #fff;
    font-size: 22px;
    line-height: 1;
    padding-left: 0;

    &::placeholder {
      font-size: 22px;
      color: rgba(0, 233, 151, .23);
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }

    @include r(374) {
      font-size: 14px;
    }
  }

  &__close {
    position: absolute;
    top: -2px;
    right: -26px;
    transform: translateY(-50%);
    width: 20px;
    height: 20px;
    cursor: pointer;

    .e-icon svg {
      fill: #00E997;
    }
  }
}
</style>



