import { INTERVAL_BALANCE_CHECK } from '@/config';
import { ASYNC_STATES, ETH_ADDRESS } from '@/util/constants';
import { onReconnect, removeReconnectListener } from '@/api/backend';
import { onResume, removeResumeListener } from '@/util/IdleEmitter';
import log from '@/util/logger';

let ethBalanceInterval;
let ethBalanceUnwatchWallet = null;
let ethBalanceUnsyncHandler;
let ethBalanceListenersCount = 0;

let tokenBalanceInterval;
let tokenBalanceUnwatchWallet = null;
let tokenBalanceUnwatchActiveToken = null;
let tokenBalanceUnsyncHandler;
let tokenBalanceListenersCount = 0;

export default {
  methods: {
    startListeningToEthBalance() {
      ethBalanceListenersCount += 1;
      if (ethBalanceListenersCount === 1) {
        const checkBalanceHandler = async () => {
          if (this.$store.getters['wallets/isLogin']) {
            const balanceApi = this.$store.getters['blockchain/balanceApi'];
            try {
              if ([ASYNC_STATES.INITIAL, ASYNC_STATES.UNSYNC].includes(this.$store.state.balance.ethInitStatus)) {
                this.$store.commit('balance/SET_BALANCE_ETH_INIT_STATUS', ASYNC_STATES.PENDING);
              }
              const baseToken = this.$store.getters['futuresActiveContract/contractBaseToken'];

              const [privateEth, tradableEth, reservedBalance, availableBalance] = await Promise.all([
                balanceApi.getPrivateEth(),
                balanceApi.getTradableEth(),
                balanceApi.getReserved({ tokenAddress: ETH_ADDRESS }),
                balanceApi.getAvailableBalance({ tokenAddress: baseToken.contract }),
              ]);

              this.$store.commit('balance/UPDATE_PRIVATE_BALANCE', { eth: privateEth });

              this.$store.commit(
                'balance/UPDATE_TRADABLE_BALANCE',
                { balance: { [ETH_ADDRESS]: tradableEth }, ...reservedBalance },
              );

              this.$store.commit('balance/UPDATE_AVAILABLE_BALANCE', { balance: { availableBalance } });

              if (this.$store.state.balance.ethInitStatus === ASYNC_STATES.PENDING) {
                this.$store.commit('balance/SET_BALANCE_ETH_INIT_STATUS', ASYNC_STATES.SUCCESS);
              }
            } catch (e) {
              // this.$store.commit('balance/SET_BALANCE_TOKEN_INIT_STATUS', ASYNC_STATES.ERROR);
              // TODO: balance update failed
              log.debug(`Eth balance update failed: ${e.message}`);
            }
          } else {
            this.$store.commit('balance/SET_BALANCE_ETH_INIT_STATUS', ASYNC_STATES.SUCCESS);
          }
        };
        checkBalanceHandler();
        ethBalanceInterval = setInterval(checkBalanceHandler, INTERVAL_BALANCE_CHECK);
        ethBalanceUnwatchWallet = this.$watch(
          () => this.$store.getters['wallets/activeWallet'],
          () => {
            this.$store.commit('balance/SET_BALANCE_ETH_INIT_STATUS', ASYNC_STATES.INITIAL);
            checkBalanceHandler();
          },
        );
        ethBalanceUnsyncHandler = () => {
          this.$store.commit('balance/SET_BALANCE_ETH_INIT_STATUS', ASYNC_STATES.UNSYNC);
          checkBalanceHandler();
        };
        onReconnect(ethBalanceUnsyncHandler);
        onResume(ethBalanceUnsyncHandler);
      }
    },

    stopListeningToEthBalance() {
      ethBalanceListenersCount -= 1;
      if (ethBalanceListenersCount === 0) {
        clearInterval(ethBalanceInterval);
        ethBalanceUnwatchWallet();
        removeReconnectListener(ethBalanceUnsyncHandler);
        removeResumeListener(ethBalanceUnsyncHandler);
        this.$store.commit('balance/SET_BALANCE_ETH_INIT_STATUS', ASYNC_STATES.INITIAL);
      }
    },

    startListeningToTokenBalance() {
      tokenBalanceListenersCount += 1;
      if (tokenBalanceListenersCount === 1) {
        const checkBalanceHandler = async () => {
          if (this.$store.getters['wallets/isLogin']) {
            if ([ASYNC_STATES.INITIAL, ASYNC_STATES.UNSYNC].includes(this.$store.state.balance.tokenInitStatus)) {
              this.$store.commit('balance/SET_BALANCE_TOKEN_INIT_STATUS', ASYNC_STATES.PENDING);
            }
            const balanceApi = this.$store.getters['blockchain/balanceApi'];
            try {
              await this.awaitSuccessOrFailure(() => this.$store.state.tokens.setActiveTokenState);
              const tokenAddress = this.$store.getters['tokens/activeTokenContract'];
              const [privateToken, tradableToken, reservedBalance] = await Promise.all([
                balanceApi.getPrivateToken({ tokenAddress }),
                balanceApi.getTradableToken({ tokenAddress }),
                balanceApi.getReserved({ tokenAddress }),
              ]);
              this.$store.commit('balance/UPDATE_PRIVATE_BALANCE', { tokens: { [tokenAddress]: privateToken } });
              this.$store.commit(
                'balance/UPDATE_TRADABLE_BALANCE',
                { balance: { [tokenAddress]: tradableToken }, ...reservedBalance },
              );
              if (this.$store.state.balance.tokenInitStatus === ASYNC_STATES.PENDING) {
                this.$store.commit('balance/SET_BALANCE_TOKEN_INIT_STATUS', ASYNC_STATES.SUCCESS);
              }
            } catch (e) {
              // TODO: balance update failed
              log.debug(`Token balance update failed: ${e.message}`);
            }
          } else {
            this.$store.commit('balance/SET_BALANCE_TOKEN_INIT_STATUS', ASYNC_STATES.SUCCESS);
          }
        };
        checkBalanceHandler();
        tokenBalanceInterval = setInterval(checkBalanceHandler, INTERVAL_BALANCE_CHECK);
        tokenBalanceUnwatchActiveToken = this.$watch(
          () => this.$store.getters['tokens/activeTokenContract'],
          () => {
            this.$store.commit('balance/SET_BALANCE_TOKEN_INIT_STATUS', ASYNC_STATES.INITIAL);
            checkBalanceHandler();
          },
        );
        tokenBalanceUnwatchWallet = this.$watch(
          () => this.$store.getters['wallets/activeWallet'],
          () => {
            this.$store.commit('balance/SET_BALANCE_TOKEN_INIT_STATUS', ASYNC_STATES.INITIAL);
            checkBalanceHandler();
          },
        );
        tokenBalanceUnsyncHandler = () => {
          this.$store.commit('balance/SET_BALANCE_TOKEN_INIT_STATUS', ASYNC_STATES.UNSYNC);
          checkBalanceHandler();
        };
        onReconnect(tokenBalanceUnsyncHandler);
        onResume(tokenBalanceUnsyncHandler);
      }
    },

    stopListeningToTokenBalance() {
      tokenBalanceListenersCount -= 1;
      if (tokenBalanceListenersCount === 0) {
        clearInterval(tokenBalanceInterval);
        tokenBalanceUnwatchWallet();
        tokenBalanceUnwatchActiveToken();
        removeReconnectListener(tokenBalanceUnsyncHandler);
        removeResumeListener(tokenBalanceUnsyncHandler);
        this.$store.commit('balance/SET_BALANCE_TOKEN_INIT_STATUS', ASYNC_STATES.INITIAL);
      }
    },

    awaitSuccessOrFailure(watchFn) {
      return new Promise((resolve, reject) => {
        let watcherExecutedSynchronously = false;
        let unwatch = null;
        unwatch = this.$watch(
          watchFn,
          (newVal) => {
            if ([ASYNC_STATES.SUCCESS, ASYNC_STATES.ERROR].includes(newVal)) {
              if (unwatch !== null) {
                unwatch();
              } else {
                watcherExecutedSynchronously = true;
              }
              if (newVal === ASYNC_STATES.SUCCESS) {
                resolve();
              } else if (newVal === ASYNC_STATES.ERROR) {
                reject();
              }
            }
          },
          { immediate: true },
        );

        if (watcherExecutedSynchronously) {
          unwatch();
        }
      });
    },
  },
};



