<template>
  <transition
    name="transition-popup"
    :duration="{ enter: 2000, leave: 600 }"
    appear
    @after-enter="$enableChat"
  >
    <div
      class="the-popup"
      :class="[isBackgroundGray ? 'the-popup_gray' : '', `the-popup__${theme}`]"
    >
      <transition-content>
        <div class="the-popup__container" :ref="'popupContainer'">
          <div class="the-popup__content">
            <transition-content
              @enter="resetAndUpdateScrollbar"
              @after-enter="resetAndUpdateScrollbar"
            >
              <router-view
                @change-content="resetAndUpdateScrollbar"
              />
            </transition-content>
          </div>
        </div>
      </transition-content>
    </div>
  </transition>
</template>

<script>
import { mapGetters } from 'vuex';

import TransitionContent from '@/containers/transitions/TransitionContent';

import { chatMixin } from '@/util/mixins';
import { POPUP_WALLET, POPUP_TOKEN_CATALOGUE, POPUP_HISTORY } from '@/store/constants';

const CONTENT_COMPONENTS_GRAY = [POPUP_WALLET, POPUP_TOKEN_CATALOGUE, POPUP_HISTORY];

export default {
  name: 'the-popup',

  computed: {
    isBackgroundGray() {
      return CONTENT_COMPONENTS_GRAY.includes(this.displayedPopup);
    },

    ...mapGetters({
      displayedPopup: 'ui/displayedPopup',
      theme: 'ui/getTheme',
    }),
  },

  created() {
    // this.$store.dispatch('balance/listenToContractBalanceChanges');
    this.$store.dispatch('deposit/listenToBalanceOperations');
  },

  beforeDestroy() {
    // this.$store.dispatch('balance/stopListeningToContractBalanceChanges');
    this.$store.dispatch('deposit/stopListeningToBalanceOperations');
  },

  methods: {
    resetScrollbar() {
      this.$nextTick(() => {
        if (this.$refs.popupContainer) {
          this.$refs.popupContainer.scrollTop = 0;
        }
      });
    },

    resetAndUpdateScrollbar() {
      this.resetScrollbar();
    },
  },

  mixins: [chatMixin],

  components: {
    TransitionContent,
  },
};

</script>

<style lang="scss">
@import '~@/scss/styles';

.the-popup {
  $this: &;

  align-items: stretch;
  height: 100%;
  width: calc(100% - 240px);
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 100;
  overflow-y: hidden;
  overflow-x: hidden;

  @include r(1023) {
    overflow-x: scroll;
  }

  &_gray {
    #{$this}__container {
      background-color: #F8F9FC;
    }
  }

  &__header {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 42px 10px 55px;
    z-index: 19;
    min-height: 40px;

    @include r(1200) {
      padding-left: 0;
      padding-right: 0;
      padding-bottom: 62px;
      margin-left: -10px;
      width: calc(100% + 20px);
      min-height: 36px;
    }
  }

  &__container {
    background-color: #f8f9fc;
    box-shadow: 0 12px 14px 0 rgba(0,0,0,0.50);
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    min-width: 773px;
    width: 100%;
    padding: 156px 6.5% 0;
    position: relative !important;
    transition-delay: 0s !important;
    overflow-y: auto;

    @include r(1200) {
      padding-top: 126px;
    }

    &::-webkit-scrollbar {
      width: 2px;
      height: 0;
    }

    &::-webkit-scrollbar-track {
      background: $color-violet;
      border-radius: 2px;
    }

    &::-webkit-scrollbar-thumb {
      background: $color-blue;
      border-radius: 2px;
    }
  }

  &__content {
    height: 100%;
    overflow: visible;
    position: relative;
    z-index: 1;
  }

  &__dark {
    #{$this}__container {
      background: #121212;

      &::-webkit-scrollbar-track {
        background-color: #212121;
      }

      &::-webkit-scrollbar-thumb {
        background-color: #5c6062;
      }
    }

    .the-wallet-pages-popup__generate-title {
      color: #8D9098;
    }
  }
}

/* transition-popup */

$delay: 0.6s;
$delay-stagger: 0.08s;

.transition-popup-enter-active {
  .the-popup__container {
    transition: 0.6s transform;
    will-change: transform;
  }

  .the-popup__container {

    > div {
      transition: 0.5s;
      transition-property: transform, opacity;
      will-change: transform, opacity;

      @for $i from 1 to 3 {
        &:nth-child(#{$i}) { transition-delay: $delay + $i * $delay-stagger; }
      }
    }
  }
}

.transition-popup-enter {
  .the-popup__container {
    transform: translateX(100%);

    > div {
      opacity: 0;
      transform: translateX(20%);
    }
  }
}

.transition-popup-leave-active {
  .the-popup__container {
    transition: 0.5s;
    transition-property: transform, opacity;
    will-change: transform, opacity;
  }
}

.transition-popup-leave-to {
  .the-popup__container {
    opacity: 0;
    transform: translateX(100%);
  }
}
</style>



