<template>
  <div class="the-mobile-token-search">
    <div class="the-mobile-token-search__label">
      search token
    </div>
    <div class="the-mobile-token-search__head">
      <div class="the-mobile-token-search__input">
        <input
          v-focus="true"
          v-model="searchString"
          type="text"
          class="the-mobile-token-search__field"
          placeholder="Find tokens by name or hash #..."
        >
        <div class="the-mobile-token-search__close" @click="searchString = ''">
          <e-icon :src="iconClose"/>
        </div>
      </div>
    </div>
    <div class="the-mobile-token-search__list">
      <div
        v-if="searchString !== ''"
        class="the-nobile-token-search__wrapper"
      >
        <the-token-search-container
          :searchString="searchString"
          :mobile="true"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mixin as focusMixin } from 'vue-focus';

import iconClose from '@/icons/e-icon_round-close.svg';

import EIcon from '@/components/EIcon';
import TheTokenSearchContainer from '@/containers/blocks/TheTokenSearchContainer';

// TODO: wait TheTokenSearchContainer

export default {
  name: 'the-mobile-token-search',

  props: {
    value: {
      type: String,
      default: '',
    },
  },

  data() {
    return {
      iconClose,
    };
  },

  computed: {
    searchString: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit('input', value);
      },
    },
  },

  methods: {
    closeMobileSearchWindow() {
      this.$emit('close');
    },
  },

  components: {
    EIcon,
    TheTokenSearchContainer,
  },

  mixins: [focusMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-mobile-token-search {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;

  &__label {
    background-color: #0a1378;
    color: rgba(129, 168, 247, 0.5);
    padding: 22px 30px 0 30px;
    text-transform: uppercase;
    font-size: 10px;
    font-weight: 500;
    line-height: 16px;
    letter-spacing: .5px;
  }

  &__head {
    background-color: #0a1378;
    height: 50px;
    padding: 6px 30px 6px 30px;
    display: flex;
    box-sizing: border-box;
  }

  &__list {
    background-color: #0a1378;
    position: relative;
    height: calc(100% - 45px);
  }

  &__wrapper {
    height: 100%;
  }

  &__input {
    width: calc(100% - 60px);
    position: relative;
    border-bottom: 2px solid #00e997;
    padding-bottom: 2px;
  }

  &__icon {
    position: absolute;
    top: 50%;
    left: 12px;
    transform: translateY(-50%);
    width: 20px;
    height: 20px;

    .e-icon svg {
      fill: white;
    }
  }

  &__field {
    width: 100%;
    background-color: transparent;
    height: 100%;
    color: #fff;
    font-size: 25px;
    line-height: 17px;

    &::placeholder {
      color: #838bc7;
    }

    @include r(374) {
      font-size: 14px;
    }
  }

  &__close {
    position: absolute;
    top: 50%;
    right: -26px;
    transform: translateY(-50%);
    height: 16px;
    width: 16px;
    cursor: pointer;

    .e-icon svg {
      fill: white;
    }
  }
}

</style>



