<template>
  <transition
    name="transition-popup"
    :duration="{ enter: 2000, leave: 600 }"
    @enter="enter"
    appear
  >
    <div class="the-menu-sidebar">
      <div class="the-menu-sidebar__body" ref="popupSidebar">
        <svg class="the-menu-sidebar__svg" width="100%" height="200%">
          <defs>
            <linearGradient id="gradient" x1="0%" x2="0%" y1="0%" y2="100%" spreadMethod="pad">
              <stop offset="0%" stop-color="#08146E" />
              <stop offset="30%" stop-color="#040D1D" />
              <stop offset="100%" stop-color="#040D1D" />
            </linearGradient>
          </defs>
          <path class="the-menu-sidebar__path" ref="sidebarPath" fill="url(#gradient)"></path>
        </svg>
        <the-menu/>
      </div>
    </div>
  </transition>
</template>

<script>
import anime from 'animejs';
import { mapGetters } from 'vuex';

import TheMenu from '@/containers/blocks/TheMenu';

export default {
  name: 'the-menu-sidebar',

  computed: {
    ...mapGetters({
      windowSize: 'ui/windowSize',
    }),
  },

  watch: {
    windowSize: {
      deep: true,
      handler() {
        this.rebuildSidebarPath();
      },
    },
  },

  methods: {
    buildPath(width, height, curve) {
      /* eslint-disable */
      let path = 'M 0 0';
      path += ` l ${width / 2} 0`;
      if (curve) {
        path += ` l 0 0`;
        path += ` q ${width} ${height / 8} 0 ${height / 4}`;
        path += ` q ${-width} ${height / 8} 0 ${height / 4}`;
        path += ` q ${width} ${height / 8} 0 ${height / 4}`;
        path += ` q ${-width} ${height / 8} 0 ${height / 4}`;
        path += ` q ${width} ${height / 8} 0 ${height / 4}`;
        path += ` l ${-width / 2} 0`;
      } else {
        path += ` l ${width / 2} 0`;
        path += ` q 0 ${height / 8} 0 ${height / 4}`;
        path += ` q 0 ${height / 8} 0 ${height / 4}`;
        path += ` q 0 ${height / 8} 0 ${height / 4}`;
        path += ` q 0 ${height / 8} 0 ${height / 4}`;
        path += ` q 0 ${height / 8} 0 ${height / 4}`;
        path += ` l ${-width} 0`;
      }
      path += ` Z`;
      return path;
      /* eslint-enable */
    },

    // Анимация при оборажении окна
    enter() {
      const { popupSidebar, sidebarPath } = this.$refs;
      const sidebarWidth = popupSidebar.offsetWidth;
      let sidebarHeight = popupSidebar.offsetHeight;
      sidebarHeight *= 2;

      sidebarPath.setAttribute('d', this.buildPath(sidebarWidth, sidebarHeight, true));

      anime({
        targets: sidebarPath,
        d: this.buildPath(sidebarWidth, sidebarHeight, false),
        duration: 1000,
        easing: 'easeOutCubic',
      });
    },

    rebuildSidebarPath() {
      const { popupSidebar, sidebarPath } = this.$refs;
      const sidebarWidth = popupSidebar.offsetWidth;
      let sidebarHeight = popupSidebar.offsetHeight;
      sidebarHeight *= 2;
      sidebarPath.setAttribute('d', this.buildPath(sidebarWidth, sidebarHeight, false));
    },
  },

  mounted() {
    this.rebuildSidebarPath();
  },

  components: {
    TheMenu,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-menu-sidebar {
  &__body {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 103;
    min-width: 240px;
    width: 240px;
  }

  &__svg {
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
  }
}

.transition-popup-enter-active {
  .the-menu-sidebar__svg {
    transition: 0.6s transform;
    will-change: transform;
  }
}

.transition-popup-enter {
  .the-menu-sidebar__svg {
    transform: translate(-100%, -50%);
  }
}

.transition-popup-leave-active {
  .the-menu-sidebar__body {
    transition: 0.5s;
    transition-property: transform, opacity;
    will-change: transform, opacity;
  }
}

.transition-popup-leave-to {
  .the-menu-sidebar__body {
    opacity: 0;
    transform: translateX(-100%);
  }
}
</style>



