<template>
  <div
    class="the-token-search-result-new"
    :class="{
      'the-token-search-result-new__light': theme === 'light',
      'the-token-search-result-new__dark': theme === 'dark'
     }"
  >
    <template v-if="notLoaded">
      <div class="the-token-search-result-new__message">
        Searching Tokens on
        <span class="the-token-search-result-new__link">EtherScan.io</span>...
      </div>
      <div class="the-token-search-result-new__wrapper">
        <div class="the-token-search-result-new__search-list">
          <e-preloader/>
        </div>
      </div>
    </template>

    <template v-else-if="tokensToDisplay.length === 0">
      <div class="the-token-search-result-new__message">
        No Tokens Found on
        <span class="the-token-search-result-new__link">EtherScan.io:</span>
      </div>
      <div class="the-token-search-result-new__recomend">
        We recomend you 3 most popular tokens
      </div>
      <div class="the-token-search-result-new__rec">
        <e-token-search-result-item
          v-for="item in recommendedTokens"
          :key="item.contract"
          v-bind="item"
          @click="handleSelect"
        />
      </div>
    </template>

    <template v-else-if="tokensToDisplay.length > 0">
      <div class="the-token-search-result-new__message">
        {{tokensToDisplay.length > 0 ? `Found ${tokensToDisplay.length}` : 'We found 0'}} Tokens on
        <span class="the-token-search-result-new__link">EtherScan.io:</span>
      </div>
      <div class="the-token-search-result-new__wrapper">
        <div
          class="the-token-search-result-new__search-list"
          :class="mobile ? 'the-token-search-result-new__search-list_height' : ''"
        >
          <e-token-search-result-item
            v-for="item in tokensToDisplay"
            :key="item.contract"
            v-bind="item"
            @click="handleSelect"
          />
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import debounce from 'lodash/debounce';
import { searchTokens } from '@/api/backend';
import { searchTokenMixin } from '@/util/mixins';
import { ASYNC_STATES } from '@/util/constants';

import EPreloader from '@/components/EPreloader';
import ETokenSearchResultItem from '@/components/ETokenSearchResultItem';

/**
 * @property requestState
 */
export default {
  name: 'the-token-search-result-new',

  props: {
    searchString: {
      type: String,
      default: '',
    },

    mobile: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      tokensToDisplay: [],
      requestState: ASYNC_STATES.INITIAL,
    };
  },

  computed: {
    ...mapGetters({
      recommendedTokens: 'tokens/recommendedTokens',
      theme: 'ui/getTheme',
    }),

    notLoaded() {
      return [ASYNC_STATES.INITIAL, ASYNC_STATES.PENDING].includes(this.requestState);
    },
  },

  watch: {
    searchString: 'handleSearchStringUpdate',
  },

  created() {
    this.updateSearchResult = debounce(this.updateSearchResult, 300, { maxWait: 300 });
  },

  mounted() {
    this.$store.dispatch('tokens/loadTokens');
    this.handleSearchStringUpdate();
  },

  beforeDestroy() {
    this.updateSearchResult.cancel();
  },

  methods: {
    handleSelect(item) {
      this.$emit('select', item);
    },

    handleSearchStringUpdate(searchString = this.searchString) {
      if (searchString) {
        // do not call delayed function if searchString is empty on initialization
        this.updateSearchResult();
      }
    },

    async updateSearchResult(searchString = this.searchString) {
      if (!searchString) return;
      this.requestState = ASYNC_STATES.PENDING;
      const request = searchTokens(searchString);
      this.actualRequest = request;
      try {
        const tokens = await this.actualRequest;
        if (request !== this.actualRequest) return;
        this.requestState = ASYNC_STATES.SUCCESS;
        this.tokensToDisplay = tokens.slice(0, 20);
      } catch (err) {
        if (request !== this.actualRequest) return;
        this.requestState = ASYNC_STATES.ERROR;
      }
    },
  },

  mixins: [searchTokenMixin],

  components: { ETokenSearchResultItem, EPreloader },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-token-search-result-new {
  $this: &;

  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;

  &__wrapper {
    display: flex;
  }

  &__search-list {
    background-color: #fff;
    min-height: 110px;
    max-height: 215px;
    width: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    position: relative;
    border-radius: 0 0 4px 4px;

    &::-webkit-scrollbar {
      position: relative;
      width: 3px;
      left: -6px;
    }

    &::-webkit-scrollbar-track {
      background-color: #fff;
      border-radius: 1px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: #979797;
      border-radius: 1px;
    }
  }

  &__search-list_height {
    max-height: 100%;
  }

  &__message {
    flex: 0 0 auto;
    height: 37px;
    background-color: #f5f5f5;
    border-radius: 4px 4px 0 0;
    color: #000;
    font-size: 10px;
    letter-spacing: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__link {
    margin-left: 5px;
    color: #1F2EE9;
  }

  &__recomend {
    color: rgba(0, 0, 0, 0.4);
    font-size: 13px;
    font-weight: normal;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 50px;
    background-color: #f5f5f5;
    letter-spacing: -.5px;
  }

  &__dark {
    #{$this}__message {
      background: #111;
      color: #5C6062;
    }

    #{$this}__recomend {

      background: #232323;
      color: #5C6062;
    }

    #{$this}__search-list {
      background: #232323;
    }

    #{$this}__link {
      color: $color-blue-dark-mode;
    }

    #{$this}__search-list {
      &::-webkit-scrollbar-track {
        background-color: #212121;
      }

      &::-webkit-scrollbar-thumb {
        background-color: #5c6062;
      }
    }
  }
}
</style>



