<template>
  <e-button
    fill="white-blue-bg"
    size="xss"
    padding="xss"
    fontWeight="400"
    iconMargin="none"
    :class="{active: copied}"
    v-clipboard:copy="copyValue"
    v-clipboard:success="onCopy"
  >
    <div class="e-button-copy__inside-btn">
      <div class="e-button-copy__btn-text" :class="!copied ? '' : 'e-button-copy__btn-text_top'">
        <span class="e-button-copy__textual">{{text}}</span>
      </div>
      <div class="e-button-copy__btn-text" :class="!copied ? '' : 'e-button-copy__btn-text_top'">
        <span class="e-button-copy__icon-like"><e-icon :src="$options.icons.iconLike"/></span>
      </div>
    </div>
  </e-button>
</template>

<script>
/**
 * Component: EButtonCopy
 *
 * @version 1.0.0
 */
import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';
import iconLike from '@/icons/e-icon_like.svg';

export default {
  name: 'e-button-copy',

  data() {
    return {
      copied: false,
    };
  },

  props: {
    copyValue: {
      type: String,
    },
    text: {
      type: String,
    },
  },

  icons: {
    iconLike,
  },

  methods: {
    onCopy() {
      if (!this.copied) {
        this.copied = true;

        setTimeout(() => {
          this.copied = false;
        }, 1500);
      }
    },
  },

  components: {
    EButton,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-button-copy {
  $this: &;

  &__inside-btn {
    height: 100%;
    top: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
  }

  &__icon-like {
    margin-left: 0;

    .e-icon {
      position: relative;
      left: 0;
      margin-right: -2px !important;
      top: -1px !important;
      width: 18px !important;
      height: 18px !important;
    }
  }

  &__btn-text {
    align-items: center;
    height: 18px;
    display: flex;
    position: relative;
    justify-content: center;
    transition: transform ease 0.3s;
  }

  &__btn-text_top {
    transform: translateY(-18px);
  }
}
</style>




