<template>
  <div
    class="the-header-futures-result"
    :class="{
      'the-header-futures-result__light': theme === 'light',
      'the-header-futures-result__dark': theme === 'dark'
     }"
  >
    <template>
      <div class="the-header-futures-result__message">
        <span>Futures Contracts</span>
      </div>

      <div class="the-header-futures-result__wrapper">
        <div class="the-header-futures-result__search-table">
          <div class="the-header-futures-result__search-table-item the-header-futures-result__search-table-thead">
            <div class="the-header-futures-result__search-table-item-block">Contract</div>
            <div class="the-header-futures-result__search-table-item-block">Price</div>
            <div class="the-header-futures-result__search-table-item-block">Volume 24h</div>
            <div class="the-header-futures-result__search-table-item-block">Leverage Buy/Sell</div>
            <div class="the-header-futures-result__search-table-item-block">Expires</div>
            <div class="the-header-futures-result__search-table-item-block">Change 24h</div>
          </div>
        </div>

        <div
          class="the-header-futures-result__search-list"
          :class="mobile ? 'the-header-futures-result__search-list_height' : ''"
        >
          <div class="the-header-futures-result__search-table">
            <e-futures-item-detail
              v-for="contract in contractsToDisplay"
              :key="contract.id"
              v-bind="contract"
              @click="handleSelect(contract)"
            />
          </div>
        </div>
      </div>

      <div class="the-header-futures-result__preloader" v-if="notLoaded">
        <e-preloader/>
      </div>
    </template>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex';
import throttle from 'lodash/throttle';

import iconQuestion from '@/icons/e-icon__question.svg';

import { getContracts, onFuturesContracts, offFuturesContracts } from '@/api/backend/futures/contracts';
import { ASYNC_STATES } from '@/util/constants';

import EIcon from '@/components/EIcon';
import EPreloader from '@/components/EPreloader';
import EFuturesItemDetail from '@/components/EFuturesItemDetail';

/**
 * @property requestState
 */
export default {
  name: 'the-header-futures-result',

  props: {
    mobile: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      contractsToDisplay: [],
      requestState: ASYNC_STATES.INITIAL,
      onFuturesContractsHandler: undefined,
      loadFuturesContractsThrottleFn: undefined,
    };
  },

  computed: {
    ...mapGetters({
      contractState: 'futuresActiveContract/contractState',
      theme: 'ui/getTheme',
    }),

    ...mapState({
      activeFuturesSmartContract: state => state.contracts.activeFuturesContract,
    }),

    notLoaded() {
      return [ASYNC_STATES.INITIAL, ASYNC_STATES.PENDING].includes(this.requestState);
    },
  },

  mounted() {
    this.loadFuturesContractsThrottleFn = throttle(this.loadFuturesContracts.bind(this), 3e3, { leading: false });

    this.$store.dispatch('tokens/loadTokens')
      .then(() => this.startListeningContractChanges())
      .then(() => this.loadFuturesContracts());
  },

  beforeDestroy() {
    this.stopListeningContractChanges();
  },

  methods: {
    handleSelect(item) {
      this.$emit('select', item);
    },

    async loadFuturesContracts() {
      this.requestState = ASYNC_STATES.PENDING;
      const request = getContracts({
        contractAddress: this.activeFuturesSmartContract.contractAddress,
        contractState: [0, 10],
        isHidden: false,
        groupByAsset: true,
      });
      this.actualRequest = request;
      try {
        const contracts = await this.actualRequest;
        if (request !== this.actualRequest) return;
        this.requestState = ASYNC_STATES.SUCCESS;

        this.contractsToDisplay = contracts.slice(0, 20);
      } catch (err) {
        if (request !== this.actualRequest) return;
        this.requestState = ASYNC_STATES.ERROR;
      }
    },

    loadFuturesContractsWithThrottle() {
      this.loadFuturesContractsThrottleFn();
    },

    startListeningContractChanges() {
      this.onFuturesContractsHandler = () => this.loadFuturesContractsWithThrottle();

      onFuturesContracts(this.onFuturesContractsHandler);
    },

    stopListeningContractChanges() {
      offFuturesContracts(this.onFuturesContractsHandler);
    },
  },

  icons: {
    iconQuestion,
  },

  components: { EIcon, EFuturesItemDetail, EPreloader },
};
</script>

<style lang="scss">
  @import '~@/scss/styles';

  .the-header-futures-result {
    $this: &;

    position: relative;
    height: 100%;
    border-radius: 0 0 5px 5px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    background: #fff;
    box-shadow: 0 12px 18px 0 rgba(17,17,17,0.20);

    &__wrapper {
      margin-right: 3px;
    }

    &__preloader {
      position: relative;
      width: 100%;
      height: 40px;
      margin-bottom: 20px;
    }

    &__rec {
      margin-right: 3px;
    }

    &__search-list {
      background-color: #fff;
      min-height: 20px;
      max-height: 232px;
      margin-right: 2px;
      width: 100%;
      overflow-y: auto;
      overflow-x: hidden;
      position: relative;
      border-radius: 0 0 4px 4px;
      margin-bottom: 5px;

      &::-webkit-scrollbar {
        position: relative;
        width: 2px;
        left: -6px;
      }

      &::-webkit-scrollbar-track {
        background: $color-violet;
        border-radius: 1px;
      }

      &::-webkit-scrollbar-thumb {
        background: $color-blue;
        border-radius: 1px;
      }
    }

    &__search-list-message {
      color: #1F2EE9;
      font-size: 12px;
      font-weight: 500;
      position: absolute;
      z-index: 1;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -12px);
      background: #fff;
      padding: 0 4px;
    }

    &__search-list_height {
      max-height: 100%;
    }

    &__message {
      flex: 0 0 auto;
      height: 37px;
      padding: 4px 0 2px;
      background-color: #f5f5f5;
      border-radius: 4px 4px 0 0;
      color: #000;
      font-size: 12px;
      font-weight: 500;
      text-transform: uppercase;
      letter-spacing: 0;
      display: flex;
      justify-content: center;
      align-items: center;

      span {
        /*margin-left: 85px;*/
      }
    }

    &__message-small {
      font-size: 10px;
      text-transform: uppercase;
      padding: 4px 0 0;
    }

    &__link {
      margin-left: 5px;
      color: #1F2EE9;
    }

    &__recomend {
      color: rgba(0, 0, 0, 0.4);
      font-size: 13px;
      font-weight: 500;
      display: flex;
      justify-content: center;
      align-items: center;
      min-height: 20px;
      margin-top: -10px;
      padding-bottom: 10px;
      background-color: #f5f5f5;
      letter-spacing: -.5px;
    }

    &__search-table {
      width: calc(100% - 9px);
      border-collapse: separate;
      border-spacing: 0 1px;
      margin: 0 3px 0 6px;
    }

    &__search-table-thead {
      font-size: 9px;
      font-weight: 500;
      text-transform: uppercase;
      color: #5C6062;
    }

    &__search-table-item {
      display: flex;
      align-items: center;
      cursor: pointer;
    }

    &__search-table-item-placeholder {
      height: 28px;
      background-color: #fff;
      border-bottom: 1px solid #e7e8eb;
    }

    &__search-table-item-block {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      padding: 4px 8px;
      box-sizing: border-box;

      &:nth-child(1) {
        justify-content: flex-start;
        flex: 0 0 15%;
      }

      &:nth-child(2) {
        flex: 0 0 15%;
        text-align: right;
      }

      &:nth-child(3) {
        flex: 0 0 15%;
        text-align: right;
      }

      &:nth-child(4) {
        flex: 0 0 25%;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
      }

      &:nth-child(5) {
        flex: 0 0 15%;
      }

      &:nth-child(6) {
        flex: 0 0 15%;
      }
    }

    &__how-to-list-token {
      height: 39px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 12px;
      text-transform: uppercase;
      font-weight: 500;
      color: #0097FE;
      margin-top: 6px;
      cursor: pointer;
      text-decoration: none;
      background-color: #f5f5f5;
      transition: background-color .15s ease-in-out;
    }

    &__how-to-list-token-icon {
      margin-right: 2px;
      width: 40px;
      height: 40px;

      svg {
        width: 40px;
        height: 47px;
        position: relative;
        top: -8px;
      }
    }

    &__placeholder {
      display: block;
      width: 50px;
      height: 8px;
      border-radius: 20px;
      opacity: 1;
      background: #E3E3EE;
    }

    &__dark {
      background: $dark-background;

      #{$this}__message {
        background: $dark-background;
        color: #fff;
      }

      #{$this}__search-table-item-block {
        margin-bottom: 1px;
      }

      #{$this}__message-empty {
        color: #5C6062;
      }

      #{$this}__recomend {
        background: $dark-background;
        color: #5C6062;
      }

      #{$this}__search-results {
        background: $dark-background;
      }

      #{$this}__search-list {
        background: $dark-background;

        &::-webkit-scrollbar-track {
          background-color: #fff;
        }

        &::-webkit-scrollbar-thumb {
          background-color: #979797;
        }
      }

      #{$this}__link {
        color: #0097FE;
      }

      #{$this}__search-list {
        &::-webkit-scrollbar-track {
          background-color: $dark-background;
        }

        &::-webkit-scrollbar-thumb {
          background-color: #5c6062;
        }
      }

      #{$this}__search-table {
        color: #fff;

        thead {
          color: #5C6062;
        }
      }

      #{$this}__how-to-list-token {
        background: #1a1a1a;

        &:hover {
          background: #232323;
        }
      }

      #{$this}__placeholder {
        background: #1f1f1f;
      }

      #{$this}__search-table-item-placeholder {
        background-color: #181818;
        border-bottom: 1px solid #101010;
      }

      #{$this}__search-list-message {
        color: #8D9098;
        background: #181818;
      }
    }
  }
</style>



