<template>
  <div
    class="e-token-search-result-item"
    :class="{
      'e-token-search-result-item__light': theme === 'light',
      'e-token-search-result-item__dark': theme === 'dark'
    }"
    @click="handleClick"
  >
    <div class="e-token-search-result-item__info">
      <div class="e-token-search-result-item__name">{{ name }}</div>
      <div class="e-token-search-result-item__address">Token: {{ shortenedAddress }}</div>
      <div
        v-if="websiteUrl"
        class="e-token-search-result-item__url"
      >
        {{ websiteUrl }}
      </div>
    </div>

    <div class="e-token-search-result-item__extended-info">
      <e-icon
        v-if="isWebsiteVerified"
        class="e-token-search-result-item__verified"
        :src="$options.icons.iconVerified"
      />
      <div class="e-token-search-result-item__symbol">{{ shortName }}</div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

import iconVerified from '@/icons/e-icon__check-flower.svg';

import ETokenIcon from '@/components/ETokenIcon';
import EIcon from '@/components/EIcon';
import EBigNumber from '@/components/EBigNumber';
import ESmallPriceChart from '@/components/ESmallPriceChart';
import ESvgFromStore from '@/components/ESvgFromStore';

const toShortenedAddress = a => `${a.slice(0, 4)}…${a.slice(-4)}`;

export default {
  name: 'e-token-search-result-item',
  inheritAttrs: false,

  props: {
    contract: {
      type: String,
      required: true,
    },

    // Название токена
    name: {
      type: String,
      required: true,
    },

    // Сокращенное название токена
    shortName: {
      type: String,
      required: true,
    },

    websiteUrl: {
      type: String,
      default: null,
    },

    isWebsiteVerified: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    shortenedAddress: vm => toShortenedAddress(vm.contract),

    ...mapGetters({
      theme: 'ui/getTheme',
    }),
  },

  icons: {
    iconVerified,
  },

  methods: {
    handleClick() {
      this.$emit('click', { contract: this.contract, name: this.name, shortName: this.shortName });
    },
  },

  components: {
    ETokenIcon,
    EIcon,
    EBigNumber,
    ESmallPriceChart,
    ESvgFromStore,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-token-search-result-item {
  $this: &;
  background-color: #fff;
  padding: 0 17px 0 12px;
  height: 52px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  user-select: none;
  transition: $transition-time;

  &:hover {
    background-color: #f3f3f3;

    #{$this}__name {
      color: #1f2ee9;
    }

    #{$this}__address {
      color: rgba(31, 46, 233, 0.4);
    }

    #{$this}__symbol {
      color: #1F2EE9;
    }
  }

  &__name {
    font-size: 12px;
    color: #071050;
    letter-spacing: 0;
    line-height: 17px;
    transition: color $transition-time;
  }

  &__address {
    font-size: 10px;
    color: rgba(0, 0, 0, 0.4);
    letter-spacing: 0;
    line-height: 1.7;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin: -2px 0 0 0;
    transition: color $transition-time;
  }

  &__url {
    font-size: 10px;
    letter-spacing: 0;
    line-height: 1.7;
    color: #1F2EE9;
    margin: -5px 0 0 0;
  }

  &__info {
    flex: 0 0 auto;
    display: flex;
    flex-flow: column;
  }

  &__extended-info {
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    overflow-x: hidden;
    max-width: 70px;
  }

  &__verified {
    width: 12px;
    height: 12px;
  }

  &__symbol {
    font-size: 14px;
    font-weight: 400;
    color: rgba(0, 0, 0, 0.4);
    letter-spacing: 0;
    line-height: (17 / 14);
    margin-left: 4px;
    transition: color $transition-time;
    overflow-x: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  &__dark {
    background-color: #232323;

    &:hover {
      background-color: #111;

      #{$this}__name {
        color: #fff;
      }

      #{$this}__address,
      #{$this}__symbol {
        color: #5C6062;
      }
    }

    #{$this}__name,
    #{$this}__symbol {
      color: #fff;
    }

    #{$this}__address {
      color: #5C6062;
    }

    #{$this}__url {
      color: $color-blue-dark-mode;
    }
  }
}
</style>



